import './index.css';

import React from 'react';
import { AlgoliaProvider } from 'leaflet-geosearch';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export class Search extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            results: [],
            isResults: false,
            onFocus: false
        };
        this._provider = new AlgoliaProvider();
    }

    async _onChange(event) {
        this.props.onChange(event);
        let results = [];
        try {
            results = await this._provider.search({ query: event.target.value });
        } catch (e) {
            results = [];
        }
        this.setState({ results });
    }

    _onEvent(event) {
        this.setState({ onFocus: false, results: [] });
        this.props.onEvent({ lat: event.y, long: event.x, address: event.label });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.clear !== this.props.clear) {
            this.setState({ onFocus: false, results: [] });
        }
    }

    render() {
        const { results, onFocus } = this.state;
        const { address, className } = this.props;
        return(
            <div id="search">
                <input
                    id='address'
                    type="text"
                    placeholder="Endereço"
                    value={address ?? ''}
                    className={className}
                    onChange={this._onChange.bind(this)}
                    onFocus={() => this.setState({ onFocus: true })}
                />
                {
                    (address && results.length > 0 && onFocus) &&
                        <div className="search__container">
                            {
                                results.map((result, index) =>
                                    <div
                                        key={index}
                                        className="search__item"
                                        onClick={() => this._onEvent(result)}
                                    >
                                        <FontAwesomeIcon icon='map-marker-alt' color="gray" /> &nbsp;
                                        {result.label}
                                    </div>
                                )
                            }
                        </div>
                }

            </div>
        )
    }

}
