import './index.css';
import React from 'react';
import Modal from 'react-awesome-modal';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/**
 * @author Gustavo
 * @helper ModalWarn helper
 */
export const ModalWarn = ({ visible, typeAnimation, onClickAway, title, subtitle, desc, oneButtonTitle, twoButtonTitle, oneButtonOnclick, oneButtonIcon }) =>
    <Modal
        width={'600'}
        height={'230'}
        visible={visible}
        effect={typeAnimation || "fadeInLeft"}
        onClickAway={onClickAway}
    >
        <div className="content__wrapper__modal">
            <div className={'header__modal__delete'}>
                <h1 className={'label__title__modal'}>
                    <FontAwesomeIcon icon={'exclamation-triangle'} />
                    <span style={{ display: 'inline', paddingLeft: '10px', fontSize: '22px' }}>
                        {title}
                    </span>
                </h1>
            </div>
            <div className={'main__modal__delete'}>
                <h2 className={'text__warn__main'} style={{ fontWeight: 'bold' }}>{subtitle}</h2>
                <h3 className={'text__warn__main'} style={{ marginTop: '-7px' }}>
                    {desc}
                </h3>
            </div>
            <div className={'container__buttons__modals'}>
                <button className={'button__delete__background'} onClick={oneButtonOnclick}>
                    <h2 className={'label__button__delete'}>
                        <FontAwesomeIcon icon={oneButtonIcon} color={'white'} />
                    </h2>
                    <h2 className={'label__button__delete'} style={{ marginLeft: '5px' }}>
                        {oneButtonTitle}
                    </h2>
                </button>
                <button className={'button__delete__background'} style={{ backgroundColor: "white", marginLeft: '15px' }} onClick={onClickAway}>
                    <h2 className={'label__button__delete'} style={{ marginLeft: '5px', color: 'black' }}>
                        {twoButtonTitle}
                    </h2>
                </button>
            </div>
        </div>
    </Modal>;

export default ModalWarn;
