import './css/index.css';
import UserIcon from '../../assets/others/user_icon.png';
/** @name Dependencies */
import React from 'react';
import { withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { StorageProfile } from './storage';
import { ItemText } from './common/textItem';
/** @name External */
import { Loader } from '../../helpers';
import { Alert } from '../../factory/alert';
import { AsyncStorage } from '../../factory';
import { StorageLogin } from '../login/storage';
import { Platform } from '../../services/platform';
import { Line, Row, Container } from '../../helpers';
/**
 * @author Hector Rodrigues Da Silva
 * @class Home
 */
class Profile extends React.PureComponent {

    constructor(props) {
        super(props);
        this._storage = AsyncStorage.getObject('user');
        this.state = {
            user: {
                email: this._storage.user.email,
                name: this._storage.user.name,
                office: this._storage.user.office
            },
            oldPass: '',
            newPass: '',
            isLoadPass: false,
            typePassowrd: true,
        };
        this._alert = new Alert();
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
        }
        this.setState({ isLoadPass: false })
    }

    _handleUser(key, value, callback) {
        this.setState(state => ({ user: { ...state.user, [key]: value } }), callback);
    }

    _onChangeUser(event) {
        this._handleUser(event.target.id, event.target.value);
    }

    async onSavedRefresh() {
        this.setState({ isLoadPass : true })

        const { newPass, oldPass } = this.state;

        let savePass = Promise.resolve();
        let saveUser = await this.onSaveUser();

        if(newPass && oldPass) {
            savePass = await this.onSavePass();
        }

        Promise.all([savePass, saveUser])
            .then(() => {
                this._alert.success('Perfil editado');
                this.setState({ isLoadPass: false })
            })
    }

    onSavePass() {
        return new Promise((resolve, reject) => {
            const { newPass, oldPass } = this.state;
            StorageProfile.refreshPass(this._storage.user.id, newPass, oldPass)
                .then(() => {
                    this.setState({ newPass: '', oldPass: '' });
                    resolve();
                })
                .catch(err =>{
                    err === 400 && this._alert.error('Senha antiga invalida!');
                    this._errStatusCode(err);
                    reject(err);
                });
        });
    }

    onSaveUser() {
        return new Promise((resolve) => {
            StorageProfile.refreshUser(this._storage.user.id, this.state.user)
                .then(() => {
                    StorageLogin.getUser()
                        .then(resp => {
                            AsyncStorage.setObject('user', resp);
                            return resolve();
                        })
                        .catch(err => this._errStatusCode(err))
                })
                .catch(err => this._errStatusCode(err))
        });
    }

    render() {
        return (
            <>
                <Container>
                    <Row>
                        <section className="col-sm-12 col-md-12 col-lg-4 col-xs-4">
                            <div className="box__user__info__profile shadow-sm">
                                <div className="content__user__profile__box">
                                    <img src={UserIcon} width={130} height={130}  alt="Profile"/>
                                    <span className="info__name__profile__user">
                                        {this.state.user.name}
                                    </span>
                                    <span className="info__email__profile__user">
                                        {this.state.user.email}
                                    </span>
                                    <div className="info__office__profile__user">
                                        <ItemText title="Cargo" info={this._storage.user.office} />
                                        <ItemText title="Login" info={this._storage.user.alias} />
                                        <ItemText title="Unidade" info={this._storage.health_unit.name} />
                                        <Line />
                                    </div>
                                </div>
                            </div>
                        </section>
                        <section className="col-sm-12 col-md-12 col-lg-8 col-xs-8">
                            <div className="box__form__info__profile shadow-sm">
                                <fieldset>
                                    <form className="needs-validation" noValidate>
                                        <div className="row">
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Nome</label>
                                                <input
                                                    id="name"
                                                    type="text"
                                                    className="form-control"
                                                    placeholder="Nome"
                                                    value={this.state.user.name}
                                                    onChange={this._onChangeUser.bind(this)}
                                                />
                                            </div>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Email</label>
                                                <input
                                                    id="email"
                                                    type="email"
                                                    className="form-control"
                                                    placeholder="Ex.: funcionario@gmail.com"
                                                    value={this.state.user.email}
                                                    onChange={this._onChangeUser.bind(this)}
                                                />
                                            </div>
                                        </div>
                                    </form>
                                </fieldset>
                                <fieldset>
                                    <form className="needs-validation" noValidate>
                                        <div className="row">
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Nova senha</label>
                                                <div className="input-group">
                                                    <input
                                                        className="form-control"
                                                        placeholder="Nova senha"
                                                        value={this.state.newPass}
                                                        type={this.state.typePassowrd ? 'password' : 'text'}
                                                        onChange={event => this.setState({ newPass: event.target.value })}
                                                    />
                                                    <div className="input-group-append">
                                                        <button
                                                            type="button"
                                                            className="btn btn-outline-secondary"
                                                            onClick={() => this.setState({ typePassowrd: !this.state.typePassowrd })}
                                                        >
                                                            <FontAwesomeIcon icon={!this.state.typePassowrd ? 'eye' : 'eye-slash'} />
                                                        </button>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Senha atual</label>
                                                <div className="input-group">
                                                    <input
                                                        className="form-control"
                                                        placeholder="Senha atual"
                                                        value={this.state.oldPass}
                                                        type={this.state.typePassowrd ? 'password' : 'text'}
                                                        onChange={event => this.setState({ oldPass: event.target.value })}
                                                    />
                                                    <div className="input-group-append">
                                                        <button
                                                            type="button"
                                                            className="btn btn-outline-secondary"
                                                            onClick={() => this.setState({ typePassowrd: !this.state.typePassowrd })}
                                                        >
                                                            <FontAwesomeIcon icon={!this.state.typePassowrd ? 'eye' : 'eye-slash'} />
                                                        </button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <button className="btn btn-dark" type="button" onClick={() => this.onSavedRefresh()}>
                                            {
                                                this.state.isLoadPass
                                                    ? <Loader size={10} color={'white'} />
                                                    : 'Salvar'
                                            }
                                        </button>
                                    </form>
                                </fieldset>
                            </div>
                        </section>
                    </Row>
                </Container>
            </>
        )
    }
}

export default withRouter(Profile);
