import { Request } from '.';
import Routes from '../../../constants/routes';

export class RequestPhoneCallsFilter extends Request {

    constructor(filter) {
        const url =`${Routes.PHONE_CALLS}?filter=${filter}`;
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        super(url, "GET", headers, null);
    }

}
