import { Fila } from '../../../factory/request/fila';
import { RequestUserRefreshPassword, RequestUserUpdate } from '../../../factory/request/request';
/**
 * @author Gustavo
 * @class StorageProfile
 */
export class StorageProfile {

    static refreshPass(id, newPass, oldPass) {
        let params = {
            new_password : newPass,
            old_password : oldPass
        };
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserRefreshPassword(id, params))
                .then(() => resolve())
                .catch(err => reject(err.response.status));
        });
    }

    static refreshUser(id, user) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserUpdate(id, user))
                .then(() => resolve())
                .catch(err => reject(err.response.status));
        });
    }

}
