import React from 'react';
import Select from 'react-select';
import { Row } from '../../../helpers';

export const BasicData = ({ onChangeFormValue, onChangeFormValueOption, infoPatient, isClassErrorsValues }) =>
    <fieldset>
        <form>
            <Row>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Nome completo:</label>
                    <input
                        type="text"
                        id="name"
                        placeholder="Nome"
                        onChange={onChangeFormValue}
                        value={infoPatient.name || ''}
                        className={`form-control ${isClassErrorsValues.name}`}
                    />
                    <div className="invalid-feedback">
                        Digite o nome do paciente.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Data de nascimento:</label>
                    <input
                        type="date"
                        id="birth"
                        placeholder="Data de nascimento"
                        onChange={onChangeFormValue}
                        value={infoPatient.birth}
                        className={`form-control ${isClassErrorsValues.birth}`}
                    />
                    <div className="invalid-feedback">
                        Digite a data de nascimento.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Endereço:</label>
                    <input
                        type="text"
                        id="address"
                        placeholder="Endereço"
                        onChange={onChangeFormValue}
                        value={infoPatient.address || ''}
                        className={`form-control ${isClassErrorsValues.address}`}
                    />
                    <div className="invalid-feedback">
                        Digite o endereço.
                    </div>
                </div>
            </Row>
            <Row>
                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                    <label>Cidade:</label>
                    <input
                        type="text"
                        id="city"
                        placeholder="Cidade"
                        value={infoPatient.city || ''}
                        onChange={onChangeFormValue}
                        className="form-control"
                    />
                </div>
                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                    <label>Telefone:</label>
                    <input
                        type="number"
                        id="telephone"
                        placeholder="Telefone"
                        onChange={onChangeFormValue}
                        value={infoPatient.telephone || ''}
                        className={`form-control ${isClassErrorsValues.telephone}`}
                    />
                    <div className="invalid-feedback">
                        Digite o telefone.
                    </div>
                </div>
            </Row>
            <Row>
                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                    <label>CNS:</label>
                    <input
                        type="number"
                        id="cns"
                        placeholder="Cartão Nacional de Saúde"
                        onChange={onChangeFormValue}
                        value={infoPatient.cns || ''}
                        className={`form-control ${isClassErrorsValues.cns}`}
                        maxLength={20}
                    />
                    <div className="invalid-feedback">
                        Digite o CNS.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                    <label>Plano de saúde:</label>
                    <Select
                        menuPortalTarget={document.body}
                        styles={{
                            menuPortal: styles => ({ ...styles, zIndex: 5000 })
                        }}
                        className={isClassErrorsValues.health_plan}
                        value={{ label: infoPatient.health_plan || 'Nenhuma opção foi selecionada', value: infoPatient.health_plan || null }}
                        options={[
                            { id: 'health_plan', value: "SUS", label: 'SUS' },
                            { id: 'health_plan', value: "Particular", label: 'Particular' }
                        ]}
                        placeholder={'Ver opções'}
                        onChange={onChangeFormValueOption}
                    />
                    <div className="invalid-feedback">
                        Seleciona alguma opção.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                    <label>Observações Gerais:</label>
                    <textarea
                        type="text"
                        id="description"
                        className="form-control"
                        placeholder="Informações relevantes"
                        onChange={onChangeFormValue}
                        value={infoPatient.description || ''}
                    />
                </div>
            </Row>
        </form>
    </fieldset>;
