import swal from "sweetalert";
import { AsyncStorage } from '../../factory';
/**
 * @author Hector Rodrigues Da Silva
 * @class Platform
 */
export class Platform {
    /**
     *
     * @param router
     */
    static logout(router) {
        AsyncStorage.clear();
        router.go('/login');
        (navigator.vendor === 'Apple Computer, Inc.') && window.location.reload();
    }

    /**
     *
     */
    static internalError() {
        swal('Erro no servidor', 'Tente novamente mais tarde!');
    }

    /**
     *
     * @param router
     * @param path
     */
    static withoutPermissionVizualization(router, path) {
        swal('Erro', 'Sem permissao para vizualizar!', "error").then(() => router.push(path));
    }

}

