import { Fila } from '../../../factory/request/fila';
import { RequestPhoneCallsRead, RequestPhoneCallsFamily, RequestPhoneCallsData, RequestPhoneCallsFilter, RequestPhoneCallsCompleted, RequestPhoneCallsCounter } from '../../../factory/request/request';
/**
 * @author Gustavo
 * @class StoragePhoneCalls
 */
export class StoragePhoneCalls {

    static getPhoneCalls() {
        return new Promise ((resolve, reject) => {
            new Fila().run(new RequestPhoneCallsRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        })
    }

    static getPhoneCallsFilter(typeFilter) {
        return new Promise ((resolve, reject) => {
            new Fila().run(new RequestPhoneCallsFilter(typeFilter))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err))
        })
    }

    static getPhoneCallsData(id) {
        return new Promise ((resolve, reject) => {
            new Fila().run(new RequestPhoneCallsData(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err))
        })
    }

    static getFamilyPhoneCalls(id){
        return new Promise((resolve, reject) =>{
            new Fila().run(new RequestPhoneCallsFamily(id))
            .then(resp => resolve(resp.data))
            .catch(err => reject(err))
        })
    }

    static getPhoneCallsCounter() {
        return new Promise ((resolve, reject) =>{
            new Fila().run(new RequestPhoneCallsCounter())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err))
        })
    }

    static setPhoneCallsCompleted(idPhoneCall, desc, id){
        return new Promise ((resolve, reject) =>{
            new Fila().run(new RequestPhoneCallsCompleted(idPhoneCall, desc, id))
                .then(() => resolve())
                .catch(err => reject(err))
        })
    }
}
