import { Fila } from '../../../factory/request/fila';
import { RequestUserCreate, RequestUserRead, RequestUserUpdate, RequestUserDelete, RequestUserData, RequestUserTransfer, RequestHealthUnitRead } from '../../../factory/request/request';
/**
 * @author Gustavo
 * @class StorageLogin
 */
export class StorageUsers {

    static getUsers() {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static getData(id) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserData(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static deleteUSer(id) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserDelete(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static editUser(id, user) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserUpdate(id, user))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static addUser(user) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserCreate(user))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static getHealthUnits() {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnitRead())
                .then(resp => {
                    let list = [];
                    resp.data.forEach(item => {
                        list.push({ value: item.id, label: item.name });
                    });
                    resolve(list);
                })
                .catch(err => reject(err.response.status));
        });
    }

    static transferUser(id, healthUnit) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestUserTransfer(id, healthUnit))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

}
