import React, {useEffect, useState} from 'react';
import ReactTooltip from "react-tooltip";
import { Table } from "../../helpers/table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const List = ({ data, onDelete, onEdit, logged, isData }) => {

    const [ size, setSize ] = useState(window.innerWidth);
    useEffect(() => {
        const resize = () => setSize(window.innerWidth);
        window.addEventListener('resize', resize);
        return function cleanup () {
            window.removeEventListener('resize', resize);
        }
    }, []);

    const hideAddress = size <= 1150;
    const hideActions = size <= 930;
    const hideMobile = size <= 640;

    const columns = [{
        dataField: 'id',
        text: 'ID',
        sort: true,
        hidden: true
    }, {
        dataField: 'name',
        text: 'Nome',
        sort: true,
    }, {
        dataField: 'address',
        text: 'Endereço',
        sort: true,
        hidden: hideAddress
    }, {
        dataField: 'city',
        text: 'Cidade',
        sort: true,
        hidden: hideMobile
    }, {
        dataField: 'telephone',
        text: 'Telefone',
        sort: true,
        hidden: hideMobile
    }, {
        dataField: 'symptomatic',
        text: 'Sintomática',
        align: 'center',
        sort: true,
        formatter: symptomaticFormat,
    }, {
        dataField: '',
        text: 'Ações',
        align: 'center',
        headerAlign: 'center',
        formatter: actionFormat,
        hidden: hideActions
    }];

    function symptomaticFormat(cel, row) {
        return(
            <FontAwesomeIcon icon={!row.symptomatic ? 'times' : 'check'} color={!row.symptomatic ? 'red' : '#1da584'} />
        );
    }

    function actionFormat(cell, row) {
        return (
            <>
                <button
                    data-for='edit'
                    data-tip='Editar'
                    onClick={() => onEdit(row)}
                    className='btn btn-sm btn-secondary'
                >
                    <FontAwesomeIcon size='xs' icon='pencil-alt' color='white' />
                </button>
                <ReactTooltip id='edit' place="top" type="dark" effect="solid" />
                <button
                    disabled={row.id === logged.id}
                    style={{ marginLeft: 5 }}
                    data-for='delete'
                    data-tip='Excluir'
                    className='btn btn-sm btn-danger'
                    onClick={() => onDelete(row)}
                >
                    <FontAwesomeIcon icon='times' color='white' />
                </button>
                <ReactTooltip id='delete' place="top" type="dark" effect="solid" />
            </>
        );
    }

    return(
        <Table
            keyField='id'
            data={data}
            isData={isData}
            columns={columns}
        />
    );
};
