import React from 'react';
import $ from 'jquery';
import { AsyncStorage } from '../../factory/storage';
import { Item } from './item';
import { Alert } from '../../factory';
import { Platform } from '../../services/platform';
import { HelpStorage } from './storage';
import { withRouter } from 'react-router-dom';
import { RegisterHelp } from './register';
import { HeaderApp, ContainerHeader, Row, Loader, ModalWarn } from '../../helpers';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/**
 * @author Hector Rodrigues Da Silva
 * @function Help
 */

let INITIAL_STATE = {
    hash: '',
    title: '',
    type: '',
}

export class Help extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            idCurrentVideo: null,
            help: {
                hash: '',
                title: '',
                type: '',
            },
            isEdit: false,
            isLoadHelp: true,
            isLoadEdit: false,
            isLoadButton: false,
            isVisibleModalDelete: false,
            isClassErrorsValues: {
                hash: '',
                title: '',
                type: ''
            }
        }
        this._swal = new Alert();
        this.storage = AsyncStorage.getObject('user');
    }

    /**
     *
     * @param err
     * @param callback
     */
    tratativeError(err, callback) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
            case 403:
                Platform.withoutPermissionVizualization(this.props.history, '/patients');
            case 409:
                this._swal.error('Vídeo já cadastrado!', () => {
                    this.setState({ cnsInvalid: true });
                });
                break;
        }
        return callback;
    }

    /**
     * Handle default to onChange
     *
     * @private
     */
    _handle(key, value, callback) {
        this.setState(state => ({ help: { ...state.help, [key]: value } }), callback);
    }

    /**
     * Handle default to error
     *
     * @private
     */
    _handleError(key, callback) {
        this.setState(state => ({ isClassErrorsValues: { ...state.isClassErrorsValues, [key]: 'is-invalid' } }), callback);
    }

    _handleClearAllErrors() {
        let defaultValues = {
            hash: '',
            title: '',
            type: ''
        }
        this.setState({ isClassErrorsValues: defaultValues });
    }

    /**
     * Handle default to clear errors
     *
     * @private
     */
    _handleClearError(key, callback) {
        this.setState(state => ({ isClassErrorsValues: { ...state.isClassErrorsValues, [key]: '' } }), callback);
    }

    _getHelps() {
        HelpStorage.getHelps()
            .then(helps => {
                this.setState({ data: helps, isLoadHelp: false });
            }).catch(err => err == 404 ? this.setState({ data: [] }) : this.tratativeError(err))
            .finally(() => this.setState({ isLoadHelp: false }));
    }

    _getHelpEditData(id) {
        this.setState({ isEdit: true, isLoadEdit: true, idCurrentVideo: id }, () => {
            HelpStorage.getHelp(id)
                .then(help => {
                    this.setState({ help: help, isLoadEdit: false });
                }).catch(err => this.tratativeError(err));
            });
    }

    onChangeValues(event) {
        this._handle(event.target.id, event.target.value);
        this._handleClearError(event.target.id)
    }

    parserUrlYouTube(url) {
        let regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
        let match = url.match(regExp);
        return (match && match[7].length == 11) ? match[7] : url;
    }

    submitVideo() {
        const { hash, title, type } = this.state.help;
        if (hash && title && type) {
            this.setState({ isLoadButton: true });
            this._handle('hash', this.parserUrlYouTube(hash), () => {
                ;
                HelpStorage.setHelp(this.state.help)
                    .then(() => {
                        this._swal.success('Vídeo cadastrado.', () => {
                            this.refresh();
                            $('#register_help').modal('hide');
                            this.setState({ help: INITIAL_STATE });
                        });
                    })
                    .catch(err => this.tratativeError(err))
                    .finally(() => this.setState({ isLoadButton: false }));
            })
        }
        !hash && this._handleError('hash');
        !title && this._handleError('title');
        !type && this._handleError('type');
    }

    onDeleteVideo(id) {
        HelpStorage.deleteHelps(id)
            .then(() => {
                this._swal.success('Vídeo deletado.', () => {
                    this.refresh();
                    this.setState({ isVisibleModalDelete: false })
                });
            }).catch(err => this.tratativeError(err))
    }

    onEditVideo() {
        const { title, type } = this.state.help;
        if (title && type) {
            let newObject = {
                title: title,
                type: type
            }
            this.setState({ isLoadButton: true });
            HelpStorage.updateHelps(newObject, this.state.idCurrentVideo)
                .then(() => {
                    this._swal.success('Vídeo editado.', () => {
                        this.refresh();
                        $('#register_help').modal('hide');
                        this.setState({ help: INITIAL_STATE , isEdit: false })
                    });
                })
                .catch(err => this.tratativeError(err))
                .finally(() => this.setState({ isLoadButton: false }));
        }
        !title && this._handleError('title');
        !type && this._handleError('type');
    }

    componentDidMount() {
        this._getHelps();
        $('#register_help')
            .on("hide.bs.modal", () =>{
                this._handleClearAllErrors();
                this.setState({ help: INITIAL_STATE, isEdit: false });
            })
    }

    refresh() {
        this._getHelps();
    }

    render() {
        return (
            <>
                <HeaderApp titleComponent="Ajuda da plataforma" />
                <ContainerHeader>
                    <RegisterHelp
                        onChangeValues={(event) => this.onChangeValues(event)}
                        submitVideo={() => this.submitVideo()}
                        isLoadButton={this.state.isLoadButton}
                        isEdit={this.state.isEdit}
                        submitEditVideo={() => this.onEditVideo()}
                        isClassErrorsValues={this.state.isClassErrorsValues}
                        isLoadEdit={this.state.isLoadEdit}
                        value={this.state.help}
                    />
                    <ModalWarn
                        subtitle={'Atenção'}
                        title={'Excluir vídeo'}
                        oneButtonTitle={'Excluir'}
                        twoButtonTitle={'Cancelar'}
                        oneButtonIcon={'check'}
                        typeAnimation={'fadeInRight'}
                        visible={this.state.isVisibleModalDelete}
                        oneButtonOnclick={() => this.onDeleteVideo(this.state.idCurrentVideo)}
                        onClickAway={() => this.setState({ isVisibleModalDelete: false })}
                        desc='Deseja excluir este vídeo de ajuda?'
                    />
                    {this.storage.user.super_admin && 
                        <Row>
                            <center>
                                <button
                                    className="btn-success"
                                    style={{
                                        border: 0,
                                        borderRadius: 100,
                                        padding: 15,
                                        paddingLeft: 20,
                                        paddingRight: 20,
                                        marginBottom: 20,
                                        backgroundColor: '#00b88a', color: 'white'
                                    }}
                                    data-target="#register_help"
                                    data-toggle="modal">
                                    <FontAwesomeIcon icon="plus" />
                                </button>
                            </center>
                        </Row>
                    }
                    <Row>
                        {this.state.isLoadHelp
                            ?
                            <center>
                                <Loader size={15} />
                            </center>

                            : this.state.data.length <= 0
                                ? <center>
                                    Nenhum vídeo encontrado.
                                    </center>
                                :
                                this.state.data.length > 0 && this.state.data.map((video, i) =>
                                    <Item
                                        key={i}
                                        hash={video.hash}
                                        title={video.title}
                                        icon={'question-circle'}
                                        permission={this.storage.user}
                                        onClickDelete={(hash) => this.setState({ isVisibleModalDelete: true, idCurrentVideo: hash })}
                                        onClickEdit={(hash) => this._getHelpEditData(hash)}
                                    />
                                )
                        }
                    </Row>
                </ContainerHeader>
            </>
        );
    }

}

export default withRouter(Help);
