/** @name Dependencies */
import $ from "jquery";
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { List } from './list';
import Register from './register';
import { StoragePhoneCalls } from './storage';
/** @name External */
import { FilterList } from '../../filters/filterTable';
import { Platform } from '../../services/platform';
import { HeaderApp, BoxInfoTable, Box, Row, ContainerHeader } from '../../helpers';
import {AsyncStorage} from "../../factory/storage";
/**
* @author Gustavo
* @class PhoneCalls
*/
class Phone extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            filter: '',
            data: [],
            patient: '',
            isData: false,
            disableEdit: false,
            counter: {
                now: 0,
                total: 0,
                family: [],
                pendent: 0,
                completed: 0,
            },
            filterPhoneCalls: 'all'
        };
        this._storage = AsyncStorage.getObject('user');
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break
        }
    }

    _filterKey(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    _setFilter(filter) {
        this.setState({ filterPhoneCalls: filter }, () => this._getPhoneCalls());
    }

    _getCounter() {
        StoragePhoneCalls.getPhoneCallsCounter()
            .then(counter => this.setState({ counter }))
            .catch(err => {
                this._errStatusCode(err)
                this.setState({ counter: { completed: 0, now: 0, pendent: 0, total: 0 } });
            })
    }

    _getPhoneCalls() {
        this._getCounter();
        this.setState({ isData: true });
        if (this.state.filterPhoneCalls === 'all') {
            StoragePhoneCalls.getPhoneCalls()
                .then(data => this.setState({ data }))
                .catch(err =>{
                    this._errStatusCode(err)
                    this.setState({ data: [] });
                })
                .finally(() => this.setState({ isData: false }));
        } else {
            StoragePhoneCalls.getPhoneCallsFilter(this.state.filterPhoneCalls)
                .then(data => this.setState({ data }))
                .catch(err => {
                    this._errStatusCode(err)
                    this.setState({ data: [] });
                })
                .finally(() => this.setState({ isData: false }));
        }
    }

    componentDidMount() {
        this._getPhoneCalls();
    }

    render() {
        const { data, filter, isData, disableEdit } = this.state;
        return (
            <div id="phonecalls">
                <HeaderApp titleComponent="Ligações" icon="phone-alt" />
                <ContainerHeader>
                    <Box
                        titleHeader='Gerenciamento de ligações.'
                        styleBox={{ borderTopLeftRadius: 7, borderTopRightRadius: 7 }}
                    >
                        <Register
                            editModal={disableEdit}
                            patient={this.state.patient}
                            refresh={this._getPhoneCalls.bind(this)}
                        />
                        <div style={{ display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap' }} >
                            <BoxInfoTable
                                title="Total"
                                icon='users'
                                count={this.state.counter.total}
                                onClick={() => this._setFilter('all')}
                                selected={this.state.filterPhoneCalls === 'all'}
                            />
                            <BoxInfoTable
                                title="Hoje"
                                icon='user-tag'
                                count={this.state.counter.now}
                                onClick={() => this._setFilter('now')}
                                selected={this.state.filterPhoneCalls === 'now'}
                            />
                            <BoxInfoTable
                                title="Pendentes"
                                icon='user-clock'
                                count={this.state.counter.pendent}
                                onClick={() => this._setFilter('pendent')}
                                selected={this.state.filterPhoneCalls === 'pendent'}
                            />
                            <BoxInfoTable
                                title="Concluídas"
                                icon='user-check'
                                count={this.state.counter.completed}
                                onClick={() => this._setFilter('completed')}
                                selected={this.state.filterPhoneCalls === 'completed'}
                            />
                        </div>
                        <Row>
                            <div
                                style={{ margin: 0, width: '100%', padding: 0 }}
                                className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-2 mt-2 "
                            >
                                <div className="input-group" style={{ width: '100%' }}>
                                    <input
                                        id="filter"
                                        type="text"
                                        className="form-control"
                                        placeholder={'Pesquisar'}
                                        onChange={this._filterKey.bind(this)}
                                    />
                                    <div className="input-group-append">
                                        <button
                                            type="button"
                                            className="btn btn-outline-secondary"
                                        >
                                            <FontAwesomeIcon icon={'search'} />
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </Row>
                        <div className='container__table' style={{ marginTop: 15 }}>
                            <List
                                isData={isData}
                                logged={this._storage.user}
                                data={FilterList(data, filter, 'name') || []}
                                onView={patient => this.setState({ patient, disableEdit: true }, () => $('#registerCall').modal('show'))}
                                onRegister={patient => this.setState({ patient, disableEdit: false }, () => $('#registerCall').modal('show'))}
                            />
                        </div>
                    </Box>
                </ContainerHeader>
            </div>
        );
    }
}

export default Phone;
