import './css/index.css';
/** @name Dependencies */
import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { StorageHistory } from './storage';
/** @name External */
import { Platform } from '../../services/platform';
import {AsyncStorage} from "../../factory/storage";
import { MomentCustom } from '../../factory/moment';
import { HeaderApp, ContainerHeader, Loader } from '../../helpers';
/**
 * @author Hector Rodrigues Da Silva
 * @class RecordActivity
 */
class RecordActivity extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            dataHistory: [],
            isLoadHistory: false
        };
        this._storage = AsyncStorage.getObject('user');
    }

    _getHistory() {
        this.setState({ isLoadHistory: true });
        StorageHistory.getHistory()
            .then(dataHistory => {
                dataHistory = StorageHistory.prepareHistory(dataHistory);
                this.setState({ dataHistory, isLoadHistory: false });
            })
            .catch(err => {
                switch (err) {
                    case 401:
                        Platform.logout(this.props.history);
                        break;
                    case 500:
                        Platform.internalError();
                        break;
                    default:
                        break;
                }
            })
            .finally(() =>  this.setState({ isLoadHistory: false }));
    }

    componentDidMount() {
        if(this._storage.user.type !== "municipal") {
            return this._getHistory();
        }
        Platform.withoutPermissionVizualization(this.props.history, '/patient');
    }

    render() {
        return (
            <>
                <HeaderApp titleComponent="Registros de atividade" icon="clipboard-list" />
                <ContainerHeader>
                    <div className="box__record__activity">
                        {this.state.isLoadHistory
                            ? <center><Loader size={10} /></center>
                            : this.state.dataHistory.length === 0 &&
                                <center>Nenhum resultado encontrado.</center>
                        }
                        <div className="content__records__app">
                            {this.state.dataHistory.map((history, i) =>
                                <Fragment key={i}>
                                    <div className="data__current__record">
                                        <span>{MomentCustom.formatCustom(history.date, "LL")}</span>
                                    </div>
                                    {
                                        history.logs.map((log, index) =>
                                            <div className="row__content__item__record" key={index}>
                                                <div className="icon__item__record">
                                                <span>
                                                    <FontAwesomeIcon icon={StorageHistory.getIcon(log.type)}/>
                                                </span>
                                                </div>
                                                <div className="item__record">
                                                    <span>
                                                        {MomentCustom.formatCustom(log.date, "HH:mm")}
                                                        &nbsp;•&nbsp;
                                                        <strong style={{ color: '#009587', fontWeight: 900 }}>
                                                            {log.name_user}
                                                        </strong>
                                                        &nbsp;{log.description}&nbsp;
                                                    </span>
                                                    <span className="hour__record">
                                                        <FontAwesomeIcon icon="clock" />
                                                        &nbsp;{MomentCustom.fromNow(log.date)}
                                                    </span>
                                                </div>
                                            </div>
                                        )
                                    }

                                </Fragment>
                            )}
                        </div>
                    </div>
                </ContainerHeader>
            </>
        );
    }
}

export default withRouter(RecordActivity);
