import $ from "jquery";
import React from 'react';
import Select from 'react-select';
import { AsyncStorage } from '../../factory';
import { StorageLogin } from '../login/storage';
import { Loader, Box, Row } from "../../helpers";
import { StorageChangeUnity } from './storage';
import { StorageHealthUnits } from '../healthunit/storage';

export class ModalTrans extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            health: null,
            healthUnits: [],
            isSaving: false,
            isHealthUnits: true,
        };
        this._storage = AsyncStorage.getObject('user');
    }

    _getHealthUnits() {
        this.setState({ health: { value: this._storage.health_current.id, label: this._storage.health_current.name } });
        this.setState({ isHealthUnits: true });
        StorageHealthUnits.getHealthUnits()
            .then(healthUnits => {
                this.transform(healthUnits)
                    .then(res => {
                        this.setState({ healthUnits: res })
                    })
            })
            .finally(() => {
                this.setState({ isHealthUnits: false })
            })
    }

    _transferUser() {
        this.setState({ isSaving: true });
        let unity = this.state.health;
        StorageChangeUnity.transPatientUnity( unity.value )
        .then(() => {
            StorageLogin.getUser()
            .then(res =>  {
                AsyncStorage.setObject('user', res);
                this._storage = AsyncStorage.getObject('user');
                window.location.reload();
            })
            .catch(err => console.log('cuzao'))
        })
        .catch(err => console.log(err))
        .finally(() => this.setState({ isSaving : false }))
    }

    transform(array) {
        return new Promise((resolve, reject) => {
            let nameUnits = [];
            array.map(item => nameUnits.push({ label: item.name, value: item.id }))
            if (nameUnits.length === array.length) resolve(nameUnits);
        })
    }

    componentDidMount() {
        console.log(AsyncStorage.getObject('user'));
        $('#ModalTrans')
            .on('hidden.bs.modal', () => {
                this.setState({ healthUnits: [], health: null });
            })
            .on('shown.bs.modal', () => {
                this._getHealthUnits();
            });
    }

    render() {
        const { health, healthUnits, isHealthUnits, isSaving } = this.state;
        return (
            <div className="modal fade" id="ModalTrans" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                            <h5 className="modal-title">
                                Trocar de Unidade
                            </h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <Box>
                                <Row>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-4">
                                        <label>Unidades de saude</label>
                                        <Select
                                            isClearable={true}
                                            value={health}
                                            options={healthUnits}
                                            isLoading={isHealthUnits}
                                            placeholder='Selecione a unidade de saude.'
                                            onChange={event => this.setState({ health: event })}
                                        />
                                    </div>
                                </Row>
                            </Box>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-dark"
                                onClick={() => this._transferUser()}
                            >
                                {isSaving ? <Loader size={8} color={'white'} /> : 'Salvar'}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
