import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestPatientDefineExam extends Request {

    constructor(idPatient, param) {
        const url = `${Routes.PATIENTS}/${idPatient}/examresult`;
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        super(url, "PATCH", headers, param);
    }

}