import RequestExecutor from '../../../factory/request/executor';
import { RequestDashboardCases, RequestDashboardTotal, RequestDashboardPhoneCalls, RequestDashboardHealthUnits, RequestDashboardAverageAge } from '../../../factory/request/request';

export class DashboardStorage {
    /**
    *
    * @return {Promise<any>}
    */
    static getHealthUnits() {
        return new Promise((resolve, reject) =>
            RequestExecutor(new RequestDashboardHealthUnits())
                .then((response) => resolve(response.data))
                .catch((err) => reject(err.response)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getPhoneCalls() {
        return new Promise((resolve, reject) =>
            RequestExecutor(new RequestDashboardPhoneCalls())
                .then((response) => resolve(response.data))
                .catch((err) => reject(err.response)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getAvarage() {
        return new Promise((resolve, reject) =>
            RequestExecutor(new RequestDashboardAverageAge())
                .then((response) => resolve(response.data))
                .catch((err) => reject(err.response)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getCases() {
        return new Promise((resolve, reject) =>
            RequestExecutor(new RequestDashboardCases())
                .then((response) => resolve(response.data))
                .catch((err) => reject(err.response)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getTotal() {
        return new Promise((resolve, reject) =>
            RequestExecutor(new RequestDashboardTotal())
                .then((response) => resolve(response.data))
                .catch((err) => reject(err.response)));
    }

}
