import './index.css';
/** @name Dependencies */
import React from 'react';
import $ from 'jquery';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name External */
import { Loader } from '../../../../helpers/loader';

/**
 * @author Gustavo
 * @helper BoxLogin helper
 */
export const BoxLogin = ({ recovery, onChangeEmail, onChangePass, auth, showPass, visiblePassword, isLoad, alertError }) =>
    <div className='container__boxLogin'>
        <div id='oneBox' className='subdivision__boxLogin'>
            <div className='container__text__informative'>
                <h1 className='text__title__boxLogin'>
                    Login
                </h1>
                <h2 className='text__subtitle__boxLogin'>
                    Somente profissionais da saúde possuem acesso na plataforma para visualizar e
                    administrar informações dos pacientes que estão em monitoramento.
                </h2>
            </div>
        </div>
        <div id='twoBox' className='subdivision__boxLogin container__boxLoginn'>
            <div className='icon__boxLogin' />
            <div className={`alert alert-${alertError.alert} alert-dismissible show mt-3 alert__${alertError.className == "login" && alertError.className}`} role="alert">
                <strong>{alertError.title}</strong>&nbsp;{alertError.message}
                <button onClick={() => $('.alert').fadeOut('slow')} type="button" className="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className='container__card__boxLogin'>
                <div className='container__input__boxLogin'>
                    <FontAwesomeIcon className='icons__input' icon="user" color={'rgba(0, 184, 138, 1'} size={'2x'} />
                    <input
                        type="text"
                        className='input'
                        placeholder='Usuário'
                        onChange={event => onChangeEmail(event.target.value)}
                    />
                </div>
            </div>
            <div className='container__card__boxLogin'>
                <div className='container__input__boxLogin'>
                    <FontAwesomeIcon className='icons__input' icon="lock" color={'rgba(0, 184, 138, 1'} size={'2x'} />
                    <input
                        className='input'
                        placeholder='Senha'
                        type={visiblePassword ? 'password' : 'text'}
                        onChange={event => onChangePass(event.target.value)}
                        onKeyPress={event => event.key === 'Enter' && auth()}
                    />
                    <FontAwesomeIcon
                        size={'2x'}
                        onClick={showPass}
                        className='icons__input'
                        style={{ cursor: 'pointer' }}
                        color={'rgba(0, 184, 138, .7'}
                        icon={visiblePassword ? "eye-slash" : "eye"}
                    />
                </div>
            </div>
            <button className='recovery__text__boxLogin' onClick={recovery}>Esqueceu sua senha?</button>
            <button
                type="submit"
                className='button__boxLogin'
                onClick={auth}
            >
                {isLoad
                    ?
                    <Loader
                        color={'white'}
                        size={10}
                    />
                    : <h1 className='label__button__boxLogin'>ENTRAR</h1>
                }
            </button>
        </div>
    </div>;
