import $ from "jquery";
import React from 'react';
import Select from 'react-select';
import { StorageUsers } from "./storage";
import { Alert } from "../../factory/alert";
import { Loader, Box, Row } from "../../helpers";
import {Platform} from "../../services/platform";

export class Transfer extends React.PureComponent{

    constructor(props) {
        super(props);
        this.state = {
            health: null,
            healthUnits: [],
            isSaving: false,
            isHealthUnits: false
        };
        this._alert = new Alert();
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
        }
    }

    _getHealthUnits() {
        this.setState({ isHealthUnits: true });
        StorageUsers.getHealthUnits()
            .then(healthUnits => this.setState({ healthUnits }))
            .catch(err => this._errStatusCode(err))
            .finally(() =>  this.setState({ isHealthUnits: false }));
    }

    _transferUser() {
        this.setState({ isSaving: true });
        StorageUsers.transferUser(this.props.userCurrent.id, this.state.health.value)
            .then(() => {
                this._alert.success('Usuario transferido!', () =>  {
                    this.props.refresh();
                    $('#transferModal').modal('hide');
                })
            })
            .catch(err => this._errStatusCode(err))
            .finally(() =>  this.setState({ isSaving: false }));
    }

    componentDidMount() {
        $('#transferModal')
            .on('hidden.bs.modal', () => {
                this.setState({ healthUnits: [], health: null });
            })
            .on('shown.bs.modal', () => {
                this._getHealthUnits();
            });
    }

    render() {
        const { health, healthUnits, isHealthUnits, isSaving } = this.state;
        return(
            <div className="modal fade" id="transferModal" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                            <h5 className="modal-title">
                                Transfirir usuario de unidade de saude.
                            </h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <Box>
                                <Row>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-4">
                                        <label>Unidades de saude</label>
                                        <Select
                                            isClearable={true}
                                            value={health}
                                            options={healthUnits}
                                            isLoading={isHealthUnits}
                                            placeholder='Selecione a unidade de saude.'
                                            onChange={event => this.setState({ health: event })}
                                        />
                                    </div>
                                </Row>
                            </Box>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-dark"
                                onClick={() => this._transferUser()}
                            >
                                {isSaving ? <Loader size={8} color={'white'} /> : 'Salvar'}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
