import { Fila } from '../../../factory/request/fila';
import {
    RequestPatientRead, RequestPatientCreate, RequestPatientData, RequestPatientCounter,
    RequestPatientDeath, RequestPatientDelete, RequestPatientDefineExam, RequestPatientFamily,
    RequestPatientUpdate, RequestPatientCalls
} from '../../../factory/request/request';

/**
 * @author Hector Rodrigues Da Silva
 * @class StorageLogin
 */
export class PatientStorage {
    /**
    *
    * @return {Promise<any>}
    */
    static getPatients() {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getUniquePatient(idPatient) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientData(idPatient))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }


    /**
    *
    * @return {Promise<any>}
    */
    static getFamilyPatient(idPatient) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientFamily(idPatient))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }
    /**
    *
    * @return {Promise<any>}
    */
    static getCallsPatient(idPatient) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientCalls(idPatient))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static getDataCounterPatients() {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientCounter())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static deletePatient(idPatient) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientDelete(idPatient))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static setDeathPatient(idPatient) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientDeath(idPatient))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static setExamResultPatient(idPatient, patientParam) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientDefineExam(idPatient, patientParam))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static insertPatient(patientParam) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientCreate(patientParam))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
    *
    * @return {Promise<any>}
    */
    static editPatient(patientParam, idCurrent) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestPatientUpdate(patientParam, idCurrent))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }
}
