import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestPhoneCallsCompleted extends Request {

    constructor(idPhoneCall, description, id_patient) {
        const url = `${Routes.PHONE_CALLS}/${idPhoneCall}/completed`;
        const headers = { 'Content-Type': 'application/x-www-form-urlencoded' };
        const params = { description, id_patient };
        super(url, "PATCH", headers, params);
    }
}
