import './aside.css';
import React from 'react';
import $ from 'jquery';
import { SideBarDesk } from './sidebardesk';
import { SideBarMobile } from './sidebarmobile';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const slideToggleMenu = () => {
    $(`.sidebar__mobile`).slideToggle();
};

export const Aside = ({ userInfo }) =>
    <>
        <button
            onClick={() => slideToggleMenu()}
            className="button__sidebar__mobile"
        >
            <FontAwesomeIcon icon="bars" />
        </button>
        <SideBarMobile userInfoSideBar={userInfo} />
        <SideBarDesk userInfoSideBar={userInfo} />
    </>;

export default Aside;
