import { Fila } from '../../../factory/request/fila';
import {
    RequestHelpUpdate,
    RequestHelpCreate,
    RequestHelpData,
    RequestHelpRead,
    RequestHelpDelete,
} from '../../../factory/request/request';

export class HelpStorage  {

    /**
     *
     * @return {Promise<any>}
     */
    static getHelps() {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHelpRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
     *
     * @return {Promise<any>}
     */
    static setHelp(params) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHelpCreate(params))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
     *
     * @return {Promise<any>}
     */
    static getHelp(id) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHelpData(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
     *
     * @return {Promise<any>}
     */
    static updateHelps(id, params) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHelpUpdate(id, params))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    /**
     *
     * @return {Promise<any>}
     */
    static deleteHelps(id) {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHelpDelete(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

}
