import React, { useEffect, useState } from 'react';
import $ from 'jquery';
import { Table } from "../../helpers";
import ReactTooltip from "react-tooltip";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const List = ({ data, logged, isData, isModalDefineExam, isModalSetDeath, isGetPatientEdit, isGetCallsPatient, isModalSetDelete, isGetFamilyPatient }) => {

    const [ size, setSize ] = useState(window.innerWidth);
    useEffect(() => {
        const resize = () => setSize(window.innerWidth);
        window.addEventListener('resize', resize);
        return function cleanup () {
            window.removeEventListener('resize', resize);
        }
    }, []);

    const hideActions = size <= 930;
    const hideMobile = size <= 640;

    const columns = [
        {
            dataField: 'name',
            text: 'Nome',
            sort: true,
            hidden: hideMobile
        }, {
            dataField: 'cns',
            text: 'CNS',
            sort: true,
        }, {
            dataField: 'telephone',
            text: 'Telefone',
            sort: true,
            hidden: hideMobile
        }, {
            headerAlign: 'center',
            dataField: 'conduct',
            text: 'Condulta',
            sort: true,
            hidden: hideMobile
        }, {
            dataField: 'in_isolation',
            align: 'center',
            text: 'Isolamento',
            sort: true,
            formatter: isolationFormatter,
            hidden: hideMobile
        }, {
            dataField: 'exam_result',
            text: 'Resultado',
            align: 'center',
            headerAlign: 'center',
            sort: true,
            formatter: resultExamFormatter,
        }, {
            dataField: 'actions',
            align: 'center',
            headerAlign: 'center',
            text: 'Ações',
            sort: true,
            formatter: actionFormat,
            hidden: hideActions
        }
    ];

    const colorByResult = (type) => {
        switch (type) {
            case 'Positivo':
                return '#ff0000';
            case 'Negativo':
                return '#00a47b';
            default:
                return '#086aff';
        }
    };

    function validateIconList(response) {
        if (response === "Positivo")
            return "exclamation-circle";
        if (response === "Negativo")
            return "virus-slash";
    }

    function isolationFormatter(cell, row) {
        return (
            <FontAwesomeIcon
                color={row.in_isolation ? '#009587' : 'black'}
                icon={row.in_isolation ? 'user-check' : 'user-times'}
            />
        );
    }

    function resultExamFormatter(cell, row) {
        return (
            <span style={{ color: colorByResult(row.exam_result) }}>
                {row.exam_result} &nbsp;
                <FontAwesomeIcon icon={validateIconList(row.exam_result) || "user-clock"} />
            </span>
        );
    }

    function actionFormat(cell, row) {
        return (
            <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'center', position: 'relative' }}>
                <div className={`list__actions action-${row.id}`} style={{ display: 'none' }}>
                    <button
                        data-for="button-death"
                        data-tip='Definir óbito'
                        className='btn btn-sm btn-dark ml-2'
                        onClick={() => isModalSetDeath({ visible: true, idPatient: row.id })}
                    >
                        <FontAwesomeIcon icon='heart-broken' color='white' />
                    </button>
                    <ReactTooltip id='button-death' place="top" type="dark" effect="solid" />
                    <button
                        data-for="button-edit"
                        data-tip={'Editar paciente'}
                        className={'btn btn-sm btn-secondary ml-2'}
                        onClick={() => isGetPatientEdit(row.id)}
                    >
                        <FontAwesomeIcon icon={'pencil-alt'} color={'white'} />
                    </button>
                    <ReactTooltip id='button-edit' place="top" type="dark" effect="solid" />
                    <button
                        data-for="button-trash"
                        data-tip='Excluir paciente'
                        className={'btn btn-sm btn-danger ml-2'}
                        onClick={() => isModalSetDelete({ visible: true, idPatient: row.id })}
                    >
                        <FontAwesomeIcon icon='times' color={'white'} />
                    </button>
                    <ReactTooltip id='button-trash' place="top" type="dark" effect="solid" />
                    {row.exam_result === "Aguardando Resultado" &&
                        <>
                            <button

                                data-toggle="modal"
                                data-for="button-option-exam"
                                data-target="#modal_define_exam"
                                data-tip='Definir exame do paciente'
                                className='btn btn-sm btn-success ml-2'
                                onClick={() => isModalDefineExam({ idPatient: row.id, namePatient: row.name })}
                            >
                                <FontAwesomeIcon icon='diagnoses' color='white' size='xs' />
                            </button>
                            <ReactTooltip id='button-option-exam' place="top" type="dark" effect="solid" />
                        </>
                    }
                    <button
                        data-target="#see_family"
                        data-toggle="modal"
                        data-for="button-see-family"
                        data-tip='Ver familiares/convivência'
                        className={'btn btn-sm btn-info ml-2'}
                        onClick={() => isGetFamilyPatient(row.id)}
                    >
                        <FontAwesomeIcon icon='users' color={'white'} />
                    </button>
                    <ReactTooltip id='button-see-family' place="top" type="dark" effect="solid" />
                    <button
                        data-target="#see_calls"
                        data-toggle="modal"
                        data-for="button-see-calls"
                        data-tip='Ver ligações do paciente'
                        className={'btn btn-sm btn-primary ml-2'}
                        onClick={() => isGetCallsPatient(row.id)}
                    >
                        <FontAwesomeIcon icon='phone-alt' color={'white'} />
                    </button>
                    <ReactTooltip id='button-see-calls' place="top" type="dark" effect="solid" />
                </div>
                <button
                    data-tip='Ações'
                    data-for='button-actions'
                    className={'btn btn-actions btn-sm btn-dark'}
                    disabled={logged.type === "municipal"}
                    onClick={() => {
                        if($(`.action-${row.id}`).is(":hidden")){
                            $('.list__actions').fadeOut();
                            $(`.action-${row.id}`).fadeIn();
                        }else{
                            $(`.action-${row.id}`).fadeOut();
                        }
                    }}
                    >
                    <FontAwesomeIcon icon='plus' color={'white'} />
                </button>
                <ReactTooltip id='button-actions' place="top" type="dark" effect="solid" />
            </div>
        );
    }

    return (
        <Table
            keyField='id'
            data={data}
            isData={isData}
            columns={columns}
        />
    );
};
