/** @name Dependencies */
import React, { Fragment } from 'react';
import $ from 'jquery';
import { MomentCustom } from '../../factory/moment';
import { Tabs, Tab, TabList, TabPanel } from 'react-tabs';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { NavTab } from './helpers/navtab';
import { PatientStorage } from './storage';
/** @name External */
import { Row, Loader } from '../../helpers';
import { Alert } from '../../factory/alert';
import { Platform } from '../../services/platform';
import { BasicData, Diagnostic, Symptoms, Family } from './common';

let INITIAL_STATE = {
    // basic data
    name: "",
    cns: "",
    birth: "",
    address: "",
    city: "",
    health_plan: "",
    telephone: "",
    description: "",
    // diagnostic
    in_isolation: "",
    first_evaluation: "",
    early_symptoms: "",
    exam_collection: "",
    exam_type: "",
    exam_result: "",
    exam_result_date: "",
    // symptoms
    comorbidities: "",
    orientation: "",
    appetite: "",
    signals: "",
    fever: "",
    cough: "",
    shortness_breath: "",
    // family
    family: []
};

export class ControlPatient extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            infoPatient: { ...INITIAL_STATE },
            isClassErrorsValues: {
                // trative values basic data
                name: '',
                cns: '',
                birth: '',
                address: '',
                health_plan: '',
                telephone: '',
                // trative values diagnostic
                in_isolation: '',
                exam_collection: '',
                exam_type: '',
                exam_result: '',
                // trative values symptoms
                fever: '',
                shortness_breath: '',
                appetite: '',
                signals: '',
                orientation: '',
                cough: ''
            },
            isTotalTabs: false,
            cnsInvalid: false,
            idCurrent: null,
            isEditPatient: false,
            isCreateFamily: false,
            selectedTabIndex: 0,
            isLoadEdit: false
        };
        this._swal = new Alert();
    }

    /**
     * Handle default to onChange
     *
     * @private
     */
    _handle(key, value, callback) {
        this.setState(state => ({ infoPatient: { ...state.infoPatient, [key]: value } }), callback);
    }

    /**
     * Handle default to error
     *
     * @private
     */
    _handleError(key, callback) {
        this.setState(state => ({ isClassErrorsValues: { ...state.isClassErrorsValues, [key]: 'is-invalid' } }), callback);
    }

    /**
     * Handle default to clear error
     *
     * @private
     */
    _handleClearError(key, callback) {
        this.setState(state => ({ isClassErrorsValues: { ...state.isClassErrorsValues, [key]: '' } }), callback);
    }

    /**
     * Handle default to clear error
     *
     * @private
     */
    _handleClearAllErrors(callback) {
        let emptyClass = {
            name: '',
            cns: '',
            birth: '',
            address: '',
            health_plan: '',
            telephone: '',
            in_isolation: '',
            exam_collection: '',
            exam_type: '',
            exam_result: '',
            fever: '',
            shortness_breath: '',
            appetite: '',
            signals: '',
            orientation: '',
            cough: ''
        };
        this.setState({ isClassErrorsValues: emptyClass }, callback);
    }

    /**
     *
     * @param err
     * @param callback
     */
    tratativeError(err, callback) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
            case 409:
                this._swal.error('CNS já cadastrado!', () => {
                    this.setState({ cnsInvalid: true });
                    this.backTabRegisterPatient(0);
                    this._handleError('cns');
                });
                break;
        }
        return callback;
    }

    /**
     *
     * @param date
     */
    formatterDate(date) {
        return MomentCustom.formatCustom(new Date(date), "YYYY-MM-DD");
    }

    /**
     *
     * @param event
     */
    onChangeMultiOptionsRegister(event) {
        if (event) {
            event.map(target => {
                let optionState = target.id === "comorbidities" ? this.state.infoPatient.comorbidities : this.state.infoPatient.signals;
                if (optionState.indexOf(target.value) === -1) {
                    this._handleClearError(target.id);
                    this._handle(target.id, !optionState ? target.value : `${optionState};${target.value}`)
                }
            });
        }
    }

    /**
     *
     * @param event
     */
    onChangeOptions(event) {
        this._handleClearError(event.id);
        this._handle(event.id, event.value);
    }

    /**
     *
     * @param event
     */
    onChangeInput(event) {
        if (event.value) {
            this._handleClearError(event.id);
            this._handle(event.id, event.value);
        } else {
            this._handle(
                event.target.id,
                event.target.id === "cns" || event.target.id === "telephone" && event.target.value.length > 15
                    ? event.target.value.substring(0, 15).replace(/([^\d])+/gim, '')
                    : event.target.value
            );
            this._handleClearError(event.target.id);
        }
    }

    /**
     *
     * @param event
     */
    onActiveFamily(event) {
        let defaultCreateFamily = { name: "", symptomatic: "", exam_result: "", kinship: "" };
        if (!event || event.value === "Não") {
            this._handle('family', [], () => this.setState({ isCreateFamily: false }));
        }
        if (event && event.value === "Sim") {
            this._handle('family', [defaultCreateFamily], () => this.setState({ isCreateFamily: true }));
        }
    }

    /**
     *
     * @param event
     * @param index
     */
    onChangeBoxFamily(event, index) {
        if (event.value) {
            this.state.infoPatient.family[index][event.id] = event.value;
            this._handle('family', this.state.infoPatient.family);
        } else {
            this.state.infoPatient.family[index][event.target.name] = event.target.value;
            this._handle('family', this.state.infoPatient.family);
        }
    }

    /**
     *
     */
    onCreateBoxFamily() {
        let defaultCreateFamily = { name: "", symptomatic: "", exam_result: "", kinship: "" };
        this._handle("family", [...this.state.infoPatient.family, defaultCreateFamily]);
    }

    /**
     *
     * @param event
     * @param index
     */
    removeBoxFamily(event, index) {
        if (this.state.infoPatient.family.length > 0) {
            this.state.infoPatient.family.splice(index, 1);
            this._handle("family", this.state.infoPatient.family, () => {
                if (this.state.infoPatient.family.length <= 0) {
                    this._handle("family", []);
                    this.setState({ isCreateFamily: false });
                }
            });
        }
    }

    /**
     *
     * @param indexTab
     */
    backTabRegisterPatient(indexTab) {
        this.setState({ selectedTabIndex: indexTab });
    }

    /**
     *
     * @param indexTab
     */
    nextTabEditPatient(indexTab) {
        const currentIndexTab = indexTab - 1;
        const {
            name, cns, birth,
            address, health_plan,
            telephone, in_isolation
        } = this.state.infoPatient;

        const nexTab = () => {
            console.log(indexTab)
            this.setState({ selectedTabIndex: indexTab });
        };

        switch (currentIndexTab) {
            case 0:
                if (name && cns && birth && address && health_plan && telephone) {
                    return nexTab();
                }
                // validation  basic data
                !name && this._handleError('name');
                !cns && this._handleError('cns');
                !birth && this._handleError('birth');
                !address && this._handleError('address');
                !health_plan && this._handleError('health_plan');
                !telephone && this._handleError('telephone');
                break;
            case 1:
                nexTab();
                break;
            case 2:
                nexTab();
                break;
        }
    }

    /**
     *
     * @param indexTab
     */
    nextTabRegisterPatient(indexTab) {
        const currentIndexTab = indexTab - 1;
        const {
            // basic data
            name, cns, birth,
            address, health_plan,
            telephone,
            // diagnostic
            in_isolation, exam_type,
            exam_collection, exam_result,
            // symptom
            fever, shortness_breath, appetite,
            signals, orientation, cough
        } = this.state.infoPatient;

        const nexTab = () => {
            this.setState({ selectedTabIndex: indexTab });
        };

        switch (currentIndexTab) {
            case 0:
                if (name && cns && birth && address && health_plan && telephone) {
                    return nexTab();
                }
                // validation  basic data
                !name && this._handleError('name');
                !cns && this._handleError('cns');
                !birth && this._handleError('birth');
                !address && this._handleError('address');
                !health_plan && this._handleError('health_plan');
                !telephone && this._handleError('telephone');
                break;
            case 1:
                if (in_isolation && exam_type && exam_collection && exam_result) {
                    return nexTab();
                }
                // validation diagnostic
                !in_isolation && this._handleError('in_isolation');
                !exam_type && this._handleError('exam_type');
                !exam_collection && this._handleError('exam_collection');
                !exam_result && this._handleError('exam_result');
                break;
            case 2:
                if (fever && shortness_breath && appetite && signals && orientation && cough) {
                    nexTab();
                    this.setState({ isTotalTabs: true })
                }
                !fever && this._handleError('fever');
                !shortness_breath && this._handleError('shortness_breath');
                !appetite && this._handleError('appetite');
                !signals && this._handleError('signals');
                !orientation && this._handleError('orientation');
                !cough && this._handleError('cough');
                break;
        }
    }

    /**
     * @param patientParam
     * @param idCurrent
     * @method put
     */
    _editPatient(patientParam, idCurrent) {
        patientParam.in_isolation = patientParam.in_isolation == 'Sim' ? 1 : 0;
        patientParam.family.map(value => {
            console.log(value.symptomatic)
            value.symptomatic = value.symptomatic == 'Assintomático' ? 0 : 1;
        })
        patientParam.family = JSON.stringify(patientParam.family);
        console.log(this.state.infoPatient);
        PatientStorage.editPatient(patientParam, idCurrent)
            .then(() => {
                this._swal.success('Paciente editado.', () => {
                    $('#control_patient').modal('hide');
                    this.props.refresh();
                })
            })
            .catch(err => this.tratativeError(err));
    }

    /**
     * @param patientParam
     * @method post
     */
    _registerPatient(patientParam) {
        console.log(patientParam.in_isolation)
        patientParam.in_isolation = patientParam.in_isolation == 'Sim' ? 1 : 0;
        patientParam.family = JSON.stringify(patientParam.family);
        PatientStorage.insertPatient(patientParam)
            .then(resp => {
                this._swal.success('Paciente cadastrado.', () => {
                    this._swal.fullinfo(`${resp.conduct}.`,
                        resp.group_risk === 'Não'
                            ? 'Paciente não se encaixa no grupo de risco.'
                            : 'Paciente se encaixa dentro do grupo de risco!', () => {
                                $('#control_patient').modal('hide');
                                this.props.refresh();
                                this.setState({ selectedTabIndex: 0, infoPatient: INITIAL_STATE, isCreateFamily: false, cnsInvalid: false },
                                    () => this.props.refresh);
                            });
                })
            }).catch(err => {
                if (err === 400) {
                    this._swal.warning('Preencha data(s) do cadastro do paciente corretamente!', () => {
                        this.setState({ selectedTabIndex: 0, isTotalTabs: true });
                    });
                } else {
                    this.tratativeError(err);
                }
            });
    }

    _submitPatient(callback) {
        const patientParam = { ...this.state.infoPatient };
        if (this.state.isCreateFamily) {
            let nameFamily;
            let symptomFamily;
            let examResultFamily;
            let typeMemberFamily;
            const newArrayPatientFamily = [...this.state.infoPatient.family];
            console.log(newArrayPatientFamily)
            newArrayPatientFamily.length > 0 && newArrayPatientFamily.map(item => {
                !item.name && this._swal.warning('Preencha o nome do familiar!');
                !item.symptomatic && this._swal.warning('Selecione o estado do familiar!');
                !item.exam_result && this._swal.warning('Selecione o resultado de exame do familiar!');
                !item.kinship && this._swal.warning('Selecione tipo de familiar do paciente!');

                !item.name && !item.symptomatic && !item.exam_result && !item.kinshiperFamily
                    && this._swal.warning('Preencha todos os campos do(s) familiar(es) adicionado(s)!');
                
                nameFamily = item.name;
                symptomFamily = item.symptomatic;
                examResultFamily = item.exam_result;
                typeMemberFamily = item.kinship;
            });

            console.log(symptomFamily)
            if (nameFamily && symptomFamily && examResultFamily && typeMemberFamily) {
                callback(patientParam);
            }
        } else {
            callback(patientParam);
        }
    }

    /**
     *
     * @param idPatient
     * @method get
     */
    _getUniquePatientEdit(idPatient) {
        this.setState({ isEditPatient: true });
        return new Promise((resolve, reject) => {
            PatientStorage.getUniquePatient(idPatient)
                .then(dataPatient => {
                    dataPatient.in_isolation = dataPatient.in_isolation ? 'Sim' : 'Não';
                    this.setState({ infoPatient: dataPatient, idCurrent: idPatient });
                    resolve(idPatient);
                }).catch(err => reject(err));
        });
    }

    _getFamilyPatientEdit(idPatient) {
        return new Promise((resolve, reject) => {
            PatientStorage.getFamilyPatient(idPatient)
                .then(dataFamily => {
                    dataFamily.map(item => {
                        item.symptomatic = item.symptomatic ? 'Sintomático' : 'Assintomático';
                    })
                    this._handle('family', dataFamily);
                    resolve();
                }).catch(err => {
                    if (err === 404) {
                        this.state.infoPatient.family = [];
                        resolve();
                    } else {
                        this.tratativeError(err);
                    }
                });
        });
    }

    componentDidMount() {
        $('#control_patient')
            .on('show.bs.modal', () => {
                console.log(this.props.isEditPatient);
                this.setState({ isEditPatient: this.props.isEditPatient }, () => {
                    if (this.state.isEditPatient) {
                        this.setState({ isLoadEdit: true });
                        this._getUniquePatientEdit(this.props.currentIdPatient)
                            .then((idPatient) => {
                                this._getFamilyPatientEdit(idPatient)
                                    .then(() => {
                                        // formatter dates to input of edit
                                        this._handle('birth', this.formatterDate(this.state.infoPatient.birth));
                                        this._handle('early_symptoms', this.formatterDate(this.state.infoPatient.early_symptoms));
                                        this._handle('exam_collection', this.formatterDate(this.state.infoPatient.exam_collection));
                                        this._handle('exam_result_date', this.formatterDate(this.state.infoPatient.exam_result_date));

                                        this.setState(this.state.infoPatient.family.length > 0 ? { isCreateFamily: true } : { isCreateFamily: false });
                                        this._handleClearAllErrors();
                                        this.setState({ isLoadEdit: false });
                                    });
                            }).catch((err) => this.tratativeError(err));
                    } else {
                        this.setState({ isLoadEdit: false });
                    }
                });
            }).on('hidden.bs.modal', () => {
                this.setState({ infoPatient: { ...INITIAL_STATE }, selectedTabIndex: 0, isEditPatient: false, isCreateFamily: false, isTotalTabs: false }, () => {
                    this.props.resetIsEditPatient();
                    this._handleClearAllErrors();
                });
            });
    }

    render() {
        const { selectedTabIndex, isEditPatient, isTotalTabs } = this.state;
        return (
            <div className="modal fade" id="control_patient" data-backdrop="static" tabIndex="-1" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document" style={{ width: '80%', maxWidth: '100%' }}>
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                            <h5 className="modal-title">{!this.state.isEditPatient ? 'Cadastrar paciente' : 'Editar paciente'}</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            {this.state.isLoadEdit
                                ?
                                <center>
                                    <Loader size={15} />
                                </center>
                                :
                                <Tabs selectedIndex={selectedTabIndex} onSelect={index =>
                                    isEditPatient
                                        ? (index === 0 ? this.backTabRegisterPatient(index) : this.nextTabEditPatient(index))
                                        : (index === 0 ? this.backTabRegisterPatient(index) : this.nextTabRegisterPatient(index))}>
                                    <TabList>
                                        <Row>
                                            {!isTotalTabs && !isEditPatient ?
                                                <>
                                                        <Tab target={0}>
                                                            <Fragment>
                                                                <>
                                                                {selectedTabIndex === 0 &&
                                                                    <NavTab text={'Dados básicos'} />
                                                                }
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                        <Tab target={1}>

                                                            <Fragment>
                                                                <>
                                                                {selectedTabIndex === 1 &&
                                                                    <NavTab text={'Diagnóstico'} />
                                                                }
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                        <Tab target={2}>
                                                            <Fragment>
                                                                <>
                                                                {selectedTabIndex === 2 &&
                                                                    <NavTab text={'Sintomas'} />
                                                                }
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                   
                                                        <Tab target={3}>
                                                            <Fragment>
                                                                <> 
                                                                {selectedTabIndex === 3 &&
                                                                    <NavTab text={'Familiares/Convivência'} />
                                                                }
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                </>
                                                :
                                                <>
                                                    <Fragment>
                                                        <Tab target={0}>
                                                            <Fragment>
                                                                <>
                                                                    <NavTab text={'Dados básicos'} />
                                                                </>

                                                            </Fragment>
                                                        </Tab>
                                                        <Tab target={1}>
                                                            <Fragment>
                                                                <>
                                                                    <NavTab text={'Diagnóstico'} />
                                                                </>
                                                            </Fragment>
                                                        </Tab>

                                                        <Tab target={2}>
                                                            <Fragment>
                                                                <>
                                                                    <NavTab text={'Sintomas'} />
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                        <Tab target={3}>

                                                            <Fragment>
                                                                <>
                                                                    <NavTab text={'Familiares/Convivência'} />
                                                                </>
                                                            </Fragment>
                                                        </Tab>
                                                    </Fragment>
                                                </>
                                            }

                                        </Row>
                                    </TabList>
                                    <TabPanel tabIndex={0}>
                                        <BasicData
                                            infoPatient={this.state.infoPatient}
                                            onChangeFormValue={this.onChangeInput.bind(this)}
                                            onChangeFormValueOption={this.onChangeOptions.bind(this)}
                                            isClassErrorsValues={this.state.isClassErrorsValues}
                                        />
                                    </TabPanel>
                                    <TabPanel tabIndex={1}>
                                        <Diagnostic
                                            infoPatient={this.state.infoPatient}
                                            onChangeFormValue={this.onChangeInput.bind(this)}
                                            onChangeFormValueOption={this.onChangeOptions.bind(this)}
                                            isClassErrorsValues={this.state.isClassErrorsValues}
                                            isEdit={this.state.isEditPatient}
                                        />
                                    </TabPanel>
                                    <TabPanel tabIndex={2}>
                                        <Symptoms
                                            infoPatient={this.state.infoPatient}
                                            onChangeFormValueOption={this.onChangeOptions.bind(this)}
                                            onChangeMultiOptionsRegister={this.onChangeMultiOptionsRegister.bind(this)}
                                            isClassErrorsValues={this.state.isClassErrorsValues}
                                            isEdit={this.state.isEditPatient}
                                        />
                                    </TabPanel>
                                    <TabPanel tabIndex={3}>
                                        <Family
                                            familyPatient={this.state.infoPatient.family}
                                            onCreateBoxFamily={this.onCreateBoxFamily.bind(this)}
                                            onChangeOptionFamily={this.onActiveFamily.bind(this)}
                                            removeBoxFamily={this.removeBoxFamily.bind(this)}
                                            isCreateFamily={this.state.isCreateFamily}
                                            onChangeBoxFamily={(event, index) => this.onChangeBoxFamily(event, index)}
                                            isClassErrorsValues={this.state.isClassErrorsValues}
                                            isFamily={this.state.isCreateFamily}
                                        />
                                    </TabPanel>
                                </Tabs>
                            }
                        </div>
                        <div className="modal-footer">
                            {selectedTabIndex <= 2
                                ?
                                <>
                                    {selectedTabIndex > 0 &&
                                        <button
                                            type="button"
                                            className="btn btn-dark"
                                            onClick={() => this.backTabRegisterPatient(selectedTabIndex - 1)}>
                                            <FontAwesomeIcon icon="angle-double-left" />
                                        &nbsp;Voltar
                                    </button>
                                    }
                                    <button
                                        type="button"
                                        className="btn"
                                        style={{ backgroundColor: '#00b88a', color: 'white' }}
                                        onClick={() => this.state.isEditPatient
                                            ? this.nextTabEditPatient(selectedTabIndex + 1)
                                            : this.nextTabRegisterPatient(selectedTabIndex + 1)}
                                    >
                                        <FontAwesomeIcon icon="angle-double-right" />
                                        &nbsp;Próximo
                                    </button>
                                    {isEditPatient && selectedTabIndex <= 2 ?
                                        <button
                                            type="button"
                                            className="btn btn-success"
                                            onClick={() => this._submitPatient((patientParam) =>
                                                this._editPatient(patientParam, this.state.idCurrent))
                                            }
                                        >
                                            <FontAwesomeIcon icon="cloud-upload-alt" />
                                            &nbsp;Salvar paciente
                                        </button>
                                    :
                                      isTotalTabs && selectedTabIndex <= 2 &&
                                        <button
                                            type="button"
                                            className="btn btn-success"
                                            onClick={() => this._submitPatient((patientParam) =>
                                                this._registerPatient(patientParam))
                                            }
                                        >
                                            <FontAwesomeIcon icon="cloud-upload-alt" />
                                            &nbsp;Cadastrar paciente
                                        </button>
                                    }
                                </>
                                :
                                <>
                                    <button
                                        type="button"
                                        className="btn btn-dark"
                                        onClick={() => this.backTabRegisterPatient(selectedTabIndex - 1)}>
                                        <FontAwesomeIcon icon="angle-double-left" />
                                        &nbsp;Voltar
                                    </button>
                                    {this.state.isEditPatient
                                        ?
                                        <button
                                            type="button"
                                            className="btn btn-success"
                                            onClick={() => this._submitPatient((patientParam) => this._editPatient(patientParam, this.state.idCurrent))}
                                        >
                                            <FontAwesomeIcon icon="cloud-upload-alt" />
                                            &nbsp;Salvar paciente
                                        </button>
                                        :
                                        <button
                                            type="button"
                                            className="btn btn-success"
                                            onClick={() => this._submitPatient((patientParam) => this._registerPatient(patientParam))}>
                                            <FontAwesomeIcon icon="cloud-upload-alt" />
                                            &nbsp;Cadastrar paciente
                                        </button>
                                    }
                                </>
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
