import './css/register.css'

import $ from "jquery";
import React from 'react';
import { Loader } from '../../helpers';
import { Alert } from '../../factory/alert';
import { withRouter } from 'react-router-dom';
import { AlertRedirect } from '../../factory/alertRedirect';
import { StoragePhoneCalls } from "./storage";
import {Platform} from "../../services/platform";

class Register extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            error: '',
            family: '',
            isSaving: false,
            isLoading: false,
            description: null,
        };
        this._alert = new Alert();
        this._alertDual = new AlertRedirect();
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break
        }
    }

    data() {
        const { patient } = this.props;
        StoragePhoneCalls.getPhoneCallsData(patient.id_phone_call)
            .then(({ description }) => this.setState({ description }))
            .catch(err => {
                this._errStatusCode(err);
                this.setState({ description: '' });
            });
    }

    register() {
        if (!this.state.description) {
            this.setState({ description: '' });
            return;
        }
        const { patient } = this.props;
        this.setState({ isSaving: true });
        StoragePhoneCalls.setPhoneCallsCompleted(patient.id_phone_call, this.state.description, patient.id)
            .then(() => {
                this._alert.success('Ligação Registrada!', () => {
                    this.props.refresh();
                    $('#registerCall').modal('hide');
                })
            })
            .catch(err => this._errStatusCode(err))
            .finally(() => this.setState({ isSaving: false }))
    }

    getFamily(id) {
        this.setState({ isLoading: true })
        StoragePhoneCalls.getFamilyPhoneCalls(id)
            .then(res => this.setState({ family: res }))
            .catch(err => {
                this._errStatusCode(err);
                this.setState({ error: 'Paciente não possui familiares' })
            })
            .finally(() => this.setState({ isLoading: false }))
    }

    componentDidMount() {
        $('#registerCall')
            .on('hidden.bs.modal', () => {
                this.setState({ description: null, family: '', error: '' });
            })
            .on('shown.bs.modal', () => {
                const { patient } = this.props;
                this.getFamily(patient.id)
                if (this.props.editModal) this.data();
            });
    }

    addPatientFamily() {
        this._alertDual.custom((value) => {
            if (value) {
                if (!this.state.description) {
                    this.setState({ description: '' });
                    return;
                }
                const { patient } = this.props;
                this.setState({ isSaving: true });
                StoragePhoneCalls.setPhoneCallsCompleted(patient.id_phone_call, this.state.description, patient.id)
                    .then(() => {
                        $('#registerCall').modal('hide');
                        this.props.history.push('/patients')
                    })
                    .catch(err => this._errStatusCode(err));
            }
        })
    }

    render() {
        const { patient, editModal } = this.props;
        const { description, isSaving, family, isLoading, error } = this.state;
        return (
            <div className="modal fade" id="registerCall" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white', flexDirection: 'column' }}>
                            <h5 className="modal-title" id="exampleModalLabel">
                                {
                                    editModal
                                        ? 'Registro de '
                                        : 'Registrar ligação de '
                                }
                                <strong>{patient.name}</strong>
                            </h5>
                            <div className={'header__modal'}>
                                <>
                                    {
                                        isLoading && !editModal ?
                                            <Loader size={8} color={'white'} />
                                            :
                                            error === '' && !editModal ? family &&
                                                <>
                                                    <h3 className={'family__title'} > Familiares </h3>
                                                    {
                                                        family.map((item, index) => (
                                                            <button
                                                                key={index}
                                                                className="label__family"
                                                                onClick={() => this.addPatientFamily(item)}
                                                            >
                                                                <h3 className={'text__name__family'}> {item.kinship}: {item.name} </h3>
                                                            </button>
                                                        ))
                                                    }
                                                </>
                                                :
                                                <div> <h3 className={'text__name__family'} style={{ color: 'white', fontSize: '12px' }}> {error} </h3> </div>
                                    }
                                </>

                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                        </div>
                        <div className="modal-body">
                            <fieldset>
                                <form>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                                        <label>Descrição da ligação</label>
                                        <textarea
                                            rows={20}
                                            type="text"
                                            value={description ?? ''}
                                            placeholder="Descrição da ligação"
                                            className={`form-control ${description === '' && 'is-invalid'}`}
                                            onChange={event => this.setState({ description: event.target.value })}
                                        />
                                        <div className="invalid-feedback">
                                            Digite a descrição da ligação.
                                        </div>
                                    </div>
                                </form>
                            </fieldset>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
                            {
                                !editModal &&
                                <button
                                    type="button"
                                    className="btn btn-dark"
                                    disabled={!description}
                                    onClick={() => this.register()}
                                >
                                    {isSaving ? <Loader size={8} color={'white'} /> : 'Salvar'}
                                </button>
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

export default withRouter(Register);
