import axios from 'axios';
import RequestExecutor from '../executor';
import { AsyncStorage } from '../../storage';
import { RequestTokenRefresh } from '../request';

export class Token {

    constructor() {
        this.refreshingToken = false;
        this.refreshTokenPromise =  Promise.resolve();
    }

    verify() {
        const user = AsyncStorage.getObject('token');

        let date = new Date();
        const dateCurrent = date.toISOString();
        console.log(dateCurrent, user.expires_in);
        if(dateCurrent >= user.expires_in && !this.refreshingToken) {

            this.refreshingToken = true;
            this.refreshTokenPromise = new Promise((resolve, reject) => {

                RequestExecutor(new RequestTokenRefresh()).then(resp => {
                    let date = new Date();
                    let expires = new Date();
                    expires.setSeconds(date.getSeconds() + (resp.data.expires_in - 10));
                    const user = {
                        access_token: resp.data.access_token,
                        refresh_token: resp.data.refresh_token,
                        expires_in: expires.toISOString()
                    };
                    axios.defaults.headers.common['Authorization'] = user.access_token;
                    AsyncStorage.setObject('token', user);
                    resolve(resp);
                }).catch(err => {
                    AsyncStorage.clear();
                    window.location.reload();
                    reject(err);
                }).finally(() => {
                    this.refreshingToken = false;
                });

            });

        }

        return this.refreshTokenPromise;
    }
}
