import '../css/about.css';
/** @name Dependencies */
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Moment from 'moment';
import 'moment/locale/pt-br';
/** @name Internal */
import { ItemDash } from '../helpers';
/**
 * @author Hector Rodrigues Da Silva
 * @class About
 */
class About extends React.PureComponent {
    constructor(props) {
        super(props);
        this.numberDayCurrent = Moment(new Date()).format("DD");
        this.textMonthCurrent = Moment().month(new Date().getMonth()).format("MMMM");
    }

    render() {
        return (
            <>
                <aside className="main__menu">
                    <div className="title__menu">
                        <h1 style={{ color: '#1da584', fontSize: 13 }}>COVID19</h1>
                        <h1>Painel&nbsp;<span style={{ fontWeight: 'normal' }}>Garça&nbsp;</span><FontAwesomeIcon icon={'shield-virus'} /></h1>
                    </div>
                    <Link to={'/'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={false}
                            title={'Dashboard'}
                            icon={'chart-area'}
                            text={'Dashboard'}
                            styleText={false}
                        />
                    </Link>
                    <Link to={'/units'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={false}
                            title={'Unidades'}
                            icon={'map-marked-alt'}
                            text={'Unidades'}
                            styleText={false}
                        />
                    </Link>
                    <ItemDash
                        className={'icon__menu-active'}
                        isTitleText={'?'}
                        title={'Sobre'}
                        icon={false}
                        text={'Sobre'}
                        styleText={false}
                    />
                    <ItemDash
                        classNameFather={'item__data'}
                        className={'icon__menu__data-disabled'}
                        isTitleText={this.numberDayCurrent}
                        title={`${this.numberDayCurrent} de ${this.textMonthCurrent}`}
                        icon={false}
                        text={this.textMonthCurrent}
                        styleText={{ color: 'rgb(248, 136, 45)' }}
                    />
                </aside>
                <main className="content__about">
                    <div className="box__about">
                        <section className="item__about">
                            <h1>Introdução</h1>
                            <p>Este sistema foi desenvolvido para ser o veículo de apoio para Unidades de Saúde de Garça sobre a situação epidemiológica da <b>COVID-19</b>.</p>
                        </section>
                        <section className="item__about">
                            <h1>Fonte</h1>
                            <p>Estimativas analíticas com base em pacientes de <b>COVID-19</b> cadastrados nas Unidades de Saúde do munícipio.</p>
                        </section>
                        <section className="item__about">
                            <h1>Atualizações</h1>
                            <p>O processo de atualização dos dados sobre casos e óbitos confirmados por <b>COVID-19</b> é realizado pelas informações cadastradas e alteradas diariamente pelas Unidades de Saúde. Os dados fornecidos são consolidados e disponibilizados publicamente todos os dias em tempo real.</p>
                        </section>
                        <section className="item__about">
                            <h1>Pacientes em acompanhamento</h1>
                            <p>São considerados como “em acompanhamento” todos os casos notificados nos últimos 14 dias pelas Unidades de Saúde e que não evoluíram para óbito. Além disso, dentre os casos que apresentaram SRAG e foram hospitalizados, considera-se “em acompanhamento” todos aqueles que foram internados nos últimos 14 dias e que não apresentam registro de alta ou óbito no SIVEP Gripe.</p>
                        </section>
                        <section className="item__about">
                            <h1>Pacientes finalizados</h1>
                            <p>Segundo a Organização Mundial da Saúde, para os casos de <b>COVID-19</b> confirmados por critério laboratorial, considera-se como pacientes finalizados aqueles que tiveram dois resultados negativos para SARS-CoV-2 com pelo menos 1 dia de intervalo. Para os casos leves, a OMS estima que tempo entre o início da infecção e a recuperação dure até 14 dias.</p>
                            <p>Como ainda há uma carência no quantitativo de testes laboratoriais disponíveis no mundo, em vários países não tem sido possível aplicar esse critério, já que o foco é identificar casos novos e isolá-los, tentando conter a transmissão.</p>
                        </section>
                        <section className="item__about">
                            <h1>Pacientes com comorbidades</h1>
                            <p>São em ocorrência de duas ou mais doenças relacionadas no mesmo paciente ao mesmo tempo. Algumas doenças comuns como transtorno de ansiedade, estresse, abuso de álcool, dor, diabetes e doenças cardiovasculares. Portanto, uma das principais características da comorbidade é que existe a possibilidade de que as doenças se potencializem mutuamente.</p>
                        </section>
                        <section className="item__about">
                            <h1>Óbitos</h1>
                            <p>Número total de óbitos confirmados por <b>COVID-19</b> que foram registrados pelas Unidades de Saúde no período considerado.</p>
                        </section>
                    </div>
                </main>
            </>
        );
    }
}

export default withRouter(About);