/**
 * @author Giovane Santos Silva
 * @class Request
 */
export class Request {
    /**
     *
     * @param url
     * @param method
     * @param headers
     * @param params
     */
    constructor(url, method, headers, params) {
        this._url = url;
        this._method = method;
        this._headers = headers;
        this._params = params;
    }

    /**
     *
     * @returns {*}
     */
    get url() {
        return this._url;
    }

    /**
     *
     * @returns {*}
     */
    get method() {
        return this._method;
    }

    /**
     *
     * @returns {*}
     */
    get headers() {
        return this._headers;
    }

    /**
     *
     * @returns {*}
     */
    get params() {
        return this._params;
    }


}
