import '../css/healthUnits.css';
import 'moment/locale/pt-br';
import 'leaflet/dist/leaflet.css';
import 'react-leaflet-markercluster/dist/styles.min.css';
/** @name Dependencies */
import React from 'react';
import Moment from 'moment';
import { Link, withRouter } from 'react-router-dom';
import MarkerClusterGroup from 'react-leaflet-markercluster';
import { Map, Marker, Popup, TileLayer } from 'react-leaflet';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { ItemDash } from '../helpers';
import { DashboardStorage } from '../storage';
/** @name External */
import { Loader } from '../../../helpers';
/**
 * @author Hector Rodrigues Da Silva
 * @class About
 */

const iconPerson = new window.L.Icon({
    iconUrl: require('../../../assets/maps/marker.png'),
    iconRetinaUrl: require('../../../assets/maps/marker.png'),
    iconSize: new window.L.Point(40, 40)
});

class healthUnits extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            isLoadMap: true,
            dataHealthUnits: []
        };
        this.numberDayCurrent = Moment(new Date()).format("DD");
        this.textMonthCurrent = Moment().month(new Date().getMonth()).format("MMMM");
    }

    componentDidMount() {
        DashboardStorage.getHealthUnits()
            .then((data) => {
                this.setState({ dataHealthUnits: data, isLoadMap: false });
            })
            .catch((err) => console.log(err))
    }

    render() {
        const { isLoadMap, dataHealthUnits } = this.state;
        return (
            <>
                <aside className="main__menu">
                    <div className="title__menu">
                        <h1 style={{ color: '#1da584', fontSize: 13 }}>COVID19</h1>
                        <h1>Painel&nbsp;<span style={{ fontWeight: 'normal' }}>Garça&nbsp;</span><FontAwesomeIcon icon={'shield-virus'} /></h1>
                    </div>
                    <Link to={'/'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={false}
                            title={'Dashboard'}
                            icon={'chart-area'}
                            text={'Dashboard'}
                            styleText={false}
                        />
                    </Link>
                    <ItemDash
                        className={'icon__menu-active'}
                        isTitleText={false}
                        title={'Unidades'}
                        icon={'map-marked-alt'}
                        text={'Unidades'}
                        styleText={false}
                    />
                    <Link to={'/about'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={'?'}
                            title={'Sobre'}
                            icon={false}
                            text={'Sobre'}
                            styleText={false}
                        />

                    </Link>
                    <ItemDash
                        classNameFather={'item__data'}
                        className={'icon__menu__data-disabled'}
                        isTitleText={this.numberDayCurrent}
                        title={`${this.numberDayCurrent} de ${this.textMonthCurrent}`}
                        icon={false}
                        text={this.textMonthCurrent}
                        styleText={{ color: 'rgb(248, 136, 45)' }}
                    />
                </aside>
                <main className="content__map">
                    {isLoadMap ?
                        <>
                            <Loader />
                        </>
                        :
                        <>
                            <Map
                                id="map__units"
                                center={[-22.2125766, -49.6547994]}
                                minZoom={2}
                                maxZoom={17}
                                zoom={12}
                                maxBoundsViscosity={1.0}
                            >
                                <TileLayer
                                    url="https://{s}.basemaps.cartocdn.com/rastertiles/voyager/{z}/{x}/{y}{r}.png"
                                    attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a>"
                                />
                                <MarkerClusterGroup>
                                    {dataHealthUnits.map((itemUnit, i) =>
                                        <div key={i}>
                                            <>
                                                <Marker
                                                    icon={iconPerson}
                                                    position={
                                                        itemUnit.latitude === 0 && itemUnit.longitude === 0
                                                            ? [-22.220341, -49.948200]
                                                            : [itemUnit.latitude, itemUnit.longitude]
                                                    }
                                                >
                                                    <Popup>
                                                        <span className="popup__health__unit"><FontAwesomeIcon icon="hospital-alt" />&nbsp;{itemUnit.name}</span>
                                                    </Popup>
                                                </Marker>
                                            </>
                                        </div>
                                    )}
                                </MarkerClusterGroup>
                            </Map>
                        </>}
                </main>
            </>
        );
    }
}

export default withRouter(healthUnits);
