import './navbar.css';
/** @name Dependencies */
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import * as Icons from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name External */
import Logo from '../../assets/logo/logo_covid.png';
import { AsyncStorage } from "../../factory/storage";
/**
 * @author Hector Rodrigues Da Silva
 * @helper NavBar
 */
const iconList = Object
    .keys(Icons)
    .filter(key => key !== "fas" && key !== "prefix")
    .map(icon => Icons[icon]);

library.add(...iconList);

export const NavBar = ({ homeApp, history }) => {
    const logout = () => {
        AsyncStorage.clear();
        history.push('/login');
    };
    return(
        <header>
            <nav className="nav__desk">
                <Link to="/" className="nav__logo">
                    <img src={Logo} className="nav__logo" alt="Logo"/>
                </Link>
                <ul>
                    {!homeApp
                        ?
                        <Link to={AsyncStorage.getObject('token') ? '/patients' : '/login'}>
                            <li><FontAwesomeIcon icon="id-badge"/>&nbsp;Entrar</li>
                        </Link>
                        :
                        <>
                            <Link to='/'>
                                <li><FontAwesomeIcon icon="chart-bar"/>&nbsp;Dashboard</li>
                            </Link>
                            <Link to='/profile'>
                                <li><FontAwesomeIcon icon="user-circle"/>&nbsp;Perfil</li>
                            </Link>
                            <li onClick={() => logout()}><FontAwesomeIcon icon="sign-out-alt"/>&nbsp;Sair</li>
                        </>
                    }
                </ul>
            </nav>
        </header>
    );
};

export default withRouter(NavBar);
