/** @name Dependencies */
import $ from 'jquery';
import React from 'react';
import { withRouter } from 'react-router-dom'
/** @name Internal */
import { List } from './list';
import { Register } from './register';
import { Transfer } from './transfer';
import { StorageUsers } from './storage';
/** @name External */
import { Alert } from '../../factory/alert';
import { FilterList } from '../../filters/filterTable';
import { Platform } from '../../services/platform';
import { AsyncStorage } from "../../factory/storage";
import { HeaderApp, ModalWarn, Box, ContainerHeader, Row, GroupSearchTable } from '../../helpers';
/**
* @author Gustavo
* @class Users
*/
class Users extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            filter: '',
            isData: true,
            showModal: false,
            userEdit: false,
            userCurrent: null
        };
        this._alert = new Alert();
        this._storage = AsyncStorage.getObject('user');
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
        }
    }

    _getUsers() {
        StorageUsers.getUsers()
            .then(data => this.setState({ data }))
            .catch(err => {
                this._errStatusCode(err);
                this.setState({ data: [] });
            })
            .finally(() => this.setState({ isData: false }));
    }

    _filterKey(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    deleteUser() {
        StorageUsers.deleteUSer(this.state.userCurrent.id)
            .then(() => this._alert.success('Usuário deletado!', () => this._getUsers()))
            .catch(err => this._errStatusCode(err))
            .finally(() => this.setState({ showModal: false }))
    }

    openDeleteModal(userCurrent) {
        this.setState({ showModal: true, userCurrent });
    }

    openRegisterModal() {
        this.setState({ userCurrent: null, userEdit: false }, () => {
            $('#registerModal').modal('show');
        });
    }

    openEditModal(userCurrent) {
        this.setState({ userCurrent, userEdit: true }, () => {
            $('#registerModal').modal('show');
        });
    }

    openTransferModal(userCurrent) {
        this.setState({ userCurrent }, () => {
            $('#transferModal').modal('show');
        });
    }

    componentDidMount() {
        if(this._storage.user.type === "admin" || this._storage.user.super_admin) {
            return this._getUsers();
        }
        Platform.withoutPermissionVizualization(this.props.history, '/patient');
    }

    render() {
        const { isData, userEdit, userCurrent } = this.state;
        return (
            <div id="user">
                <HeaderApp titleComponent="Usuários" icon="user-friends"/>
                <ModalWarn
                    subtitle='Deletar'
                    title='Deletar Usuário'
                    oneButtonTitle='Confirmar'
                    twoButtonTitle='Cancelar'
                    oneButtonIcon='trash-alt'
                    visible={this.state.showModal}
                    oneButtonOnclick={() => this.deleteUser()}
                    onClickAway={() => this.setState({ showModal: false })}
                    desc='Ao confirmar esta ação, o usuário será excluso do sistema sem chances de recuperação de dados, deseja realmente continuar?'
                />
                <Register
                    userEdit={userEdit}
                    userCurrent={userCurrent}
                    refresh={this._getUsers.bind(this)}
                />
                <Transfer
                    userCurrent={userCurrent}
                    refresh={this._getUsers.bind(this)}
                />
                <ContainerHeader>
                    <Box
                        titleHeader='Gerenciamento de usuários da plataforma.'
                        styleBox={{ borderTopLeftRadius: 7, borderTopRightRadius: 7 }}
                    >
                        <Row>
                            <GroupSearchTable
                                placeholderSearch="Pesquisar"
                                onFilter={this._filterKey.bind(this)}
                                onClickNewButton={this.openRegisterModal.bind(this)}
                            />
                        </Row>
                        <div className="container__table" style={{ marginTop: 15 }}>
                            <List
                                isData={isData}
                                logged={this._storage.user}
                                onEdit={this.openEditModal.bind(this)}
                                onDelete={this.openDeleteModal.bind(this)}
                                onTransfer={this.openTransferModal.bind(this)}
                                data={FilterList(this.state.data, this.state.filter, 'name', 'email') || []}
                            />
                        </div>
                    </Box>
                </ContainerHeader>
            </div>
        );
    }
}

export default withRouter(Users);
