import axios from 'axios';
import React from 'react';
import { AsyncStorage } from "../factory/storage";
import { HashRouter as Router, Route, Redirect, Switch } from 'react-router-dom';

import App from './app/app';
import DashboardApp from './dashboardApp/dashboard';// template dashboard
// modules dashboard
import DashboardContent from '../modules/dashboard';
import AboutDashboardContent from '../modules/dashboard/about';
import HealthUnitsDashboardContent from '../modules/dashboard/healthUnits';
// modules app
import Users from '../modules/users';
import Login from '../modules/login';
import Patients from '../modules/patients';
import Phone from '../modules/phonecalls';
import healthUnit from '../modules/healthunit';
import ProfileContent from '../modules/profile';
import RecordActivity from '../modules/history';
import Help from '../modules/help';

const isAuthenticated = () => AsyncStorage.getObject('token');
axios.defaults.headers.common['Authorization'] = isAuthenticated()?.access_token;
/**
 * @author Hector Rodrigues Da Silva
 * @ROUTES
 */
export default () => {
  return (
    <Router>
      <Switch>
        {/* DASHBOARD */}
        <DashRoute exact path="/" component={DashboardContent}/>
        <DashRoute exact path="/about" component={AboutDashboardContent}/>
        <DashRoute exact path="/units" component={HealthUnitsDashboardContent}/>
        {/* APP */}
        <Route path="/login" component={Login} />
        <PrivateRoute path="/users" component={Users} />
        <PrivateRoute path="/patients" component={Patients} />
        <PrivateRoute path="/phonecalls" component={Phone} />
        <PrivateRoute path="/healthunit" component={healthUnit} />
        <PrivateRoute path="/profile" component={ProfileContent} />
        <PrivateRoute path="/record/activity" component={RecordActivity} />
        <PrivateRoute path="/help" component={Help} />
        <Redirect path="*" to="/" />
      </Switch>
    </Router>
  );
}

const DashRoute = ({ component: Component, ...rest }) => (
    <Route
        {...rest}
        render={props =>
            <DashboardApp>
                <Component {...props} />
            </DashboardApp>
        }
    />
);

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
      {...rest}
      render={props =>
         isAuthenticated() ?
            <App>
                <Component {...props} />
            </App>
         :
             <Redirect to="/login" />
      }
  />
);
