import './css/index.css';
/** @name Dependencies */
import $ from 'jquery';
import axios from "axios";
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name External */
import { StorageLogin } from './storage';
import { AsyncStorage } from '../../factory/storage';
import { BoxLogin, BoxEmailRecovery, BoxCodeRecovery, BoxPasswordRecovery } from './helpers';
/**
* @author Gustavo
* @class Login
*/
class Login extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            isScroll: 0,
            isLoading: false,
            isCarrosel: false,
            //login
            isEmail: '',
            isPassword: '',
            isShowPass: true,
            alertError: {
                alert: null,
                title: null,
                message: null,
            },
            //email
            isEmailRecovery: '',
            //code
            isCode: 0,
            //recovery Password
            isTwoPass: '',
            isOnePass: '',
            isShowRecovery: true,
        };
        this._average = /(?=.*[a-z])(?=.*[0-9])(?=.{6,}).*/;
    }

    /**
     *
     * @param err
     * @param classNameAlert
     * @param titleNotFound
     * @param messageNotFound
     */
    tratativeDefaultError(err, classNameAlert, titleNotFound, messageNotFound) {
        switch (err) {
            case 500:
                this.alertLogin('danger', classNameAlert, 'Erro no servidor!', 'Tente novamente mais tarde!');
                break;
            case 400:
                this.alertLogin('danger', classNameAlert, 'Erro no cliente!', 'Tente novamente mais tarde!');
                break;
            default:
                this.alertLogin('danger', classNameAlert, !titleNotFound ? 'Erro!' : titleNotFound, !messageNotFound ? 'Nada foi encontrado' : messageNotFound);
                break;
        }
    }

    alertLogin(typeAlert, className, title, message) {
        $('.alert').css('display', 'none');
        this.setState({ alertError: { alert: typeAlert, className: className, title: title, message: message } },
            () => $(`.alert__${className}`).fadeIn('slow'));
    }

    login(isEmail = this.state.isEmail, isPassword = this.state.isPassword) {
        if (!isEmail || !isPassword) {
            this.alertLogin('warning', 'login', 'Atenção!', 'Preencha todos os campos!');
        } else {
            this.setState({ isLoading: true });
            StorageLogin.authToken(isEmail, isPassword)
                .then(resp => {
                    let date = new Date();
                    let expires = new Date();
                    expires.setSeconds(date.getSeconds() + (resp.expires_in - 10));
                    const user = {
                        access_token: resp.access_token,
                        refresh_token: resp.refresh_token,
                        expires_in: expires.toISOString()
                    };
                    axios.defaults.headers.common['Authorization'] = user.access_token;
                    AsyncStorage.setObject('token', user);
                })
                .then(() => {
                    StorageLogin.getUser()
                        .then(resp => {
                            AsyncStorage.setObject('user', resp);
                            this.props.history.push('/patients');
                        }).catch(err => this.tratativeDefaultError(err, 'error-default', null, null));
                }).catch(err => {
                    this.tratativeDefaultError(err, 'login', 'Erro!', 'Usuário ou senha incorreta.');
                }).finally(() => {
                    this.setState({ isLoading: false });
                })
        }
    }

    recoveryEmail() {
        if (!this.state.isEmailRecovery) {
            this.alertLogin('warning', 'email', 'Atenção!', 'Preencha o campo com seu E-mail');
        } else {
            this.setState({ isLoading: true })
            StorageLogin.recoveryEmail(this.state.isEmailRecovery)
                .then(() => {
                    $('.alert').fadeOut('slow');
                    this.moveCarousel(2)
                }).catch((err) => {
                    this.tratativeDefaultError(err, 'email', 'Erro!', 'E-mail de usuário não encontrado.');
                }).finally(() => {
                    this.setState({ isLoading: false })
                });
        }
    }

    recoveryCode() {
        if (!this.state.isCode) {
            this.alertLogin('warning', 'code', 'Ops!', 'Preencha o campo com o seu código.');
        } else {
            this.setState({ isLoading: true })
            StorageLogin.recoveryCode(this.state.isCode)
                .then(() => {
                    this.moveCarousel(3)
                }).catch((err) => {
                    this.tratativeDefaultError(err, 'code', 'Erro!', 'Código de recuperação inválido.');
                }).finally(() => {
                    this.setState({ isLoading: false })
                });
        }
    }

    recoveryPass() {
        if (!this.state.isOnePass || !this.state.isTwoPass || this.state.isOnePass !== this.state.isTwoPass) {
            this.alertLogin('warning', 'password', 'Atenção!', 'Preencha o campo de senha corretamente.');
        } else if (!this._average.test(this.state.isOnePass)) {
            this.alertLogin('warning', 'password', 'Atenção!', 'Senha muito fraca, crie uma conforme os requisitos.');
        } else {
            this.setState({ isLoading: true });
            StorageLogin.recoveryPass(this.state.isOnePass, this.state.isCode)
                .then(() => {
                    this.login(this.state.isEmailRecovery, this.state.isOnePass)
                }).catch(err => {
                    this.tratativeDefaultError(err, null, null)
                }).finally(() => {
                    this.setState({ isLoading: false });
                })
        }
    }

    moveCarousel(number) {
        this.setState({ isScroll: number });
    }

    componentDidMount() {
        document.onkeydown = (e) => {
            if (e.key === 'Tab') {
                return false;
            }
        }
    }

    render() {
        return (
            <>
                <Link to="/">
                    <button className="back__dashboard">
                        <span><FontAwesomeIcon icon="angle-left" /></span>
                    </button>
                </Link>
                <div className="container__all">
                    <div className={'body__login'}>
                        <div className='carousel'>
                            <div id='realist__carousel'>
                                <div className={StorageLogin.scrollCarousel(this.state.isScroll)}>
                                    <BoxLogin
                                        alertError={this.state.alertError}
                                        isLoad={this.state.isLoading}
                                        auth={() => this.login()}
                                        recovery={() => this.moveCarousel(1)}
                                        visiblePassword={this.state.isShowPass}
                                        onChangeEmail={isEmail => this.setState({ isEmail })}
                                        onChangePass={isPassword => this.setState({ isPassword })}
                                        showPass={() => this.setState({ isShowPass: !this.state.isShowPass })}
                                    />
                                    <BoxEmailRecovery
                                        alertError={this.state.alertError}
                                        load={this.state.isLoading}
                                        nextRecovery={() => this.recoveryEmail()}
                                        backRecovery={() => this.moveCarousel(0)}
                                        onChangeEmail={isEmailRecovery => this.setState({ isEmailRecovery })}
                                    />
                                    <BoxCodeRecovery
                                        alertError={this.state.alertError}
                                        load={this.state.isLoading}
                                        refreshCode={() => this.recoveryEmail()}
                                        nextRecovery={() => this.recoveryCode()}
                                        backRecovery={() => this.moveCarousel(0)}
                                        onChangeCode={isCode => this.setState({ isCode })}
                                    />
                                    <BoxPasswordRecovery
                                        alertError={this.state.alertError}
                                        load={this.state.isLoading}
                                        nextRecovery={() => this.recoveryPass()}
                                        visiblePassword={this.state.isShowRecovery}
                                        backRecovery={() => this.moveCarousel(0)}
                                        onChangeTwo={isTwoPass => this.setState({ isTwoPass })}
                                        onChangeOne={isOnePass => this.setState({ isOnePass })}
                                        showPass={() => this.setState({ isShowRecovery: !this.state.isShowRecovery })}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default withRouter(Login);
