import $ from "jquery";
import React from 'react';
import Select from 'react-select';
import { Alert } from "../../factory/alert";
import { StorageHealthUnits } from "./storage";
import { Row, Loader, Search } from '../../helpers';

const INITIAL_STATE = {
    name: null,
    email: null,
    latitude: 0,
    clear: false,
    longitude: 0,
    city: 'Marilía',
    address: null,
    isSaving: false,
    telephone: null,
    symptomatic: null,
};

export class Register extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            ...INITIAL_STATE
        };
        this._optionsUnit = [
            { value: true, label: 'Unidade Sintomática' },
            { value: false, label: 'Unidade Assintomática' },
        ];
        this._alert = new Alert();
    }

    _onChange(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    _onChangeLatLong({ lat, long, address }) {
        this.setState({ latitude: lat, longitude: long, address });
    }

    _validateRegister() {
        const { name, email } = this.state;
        if(!name) this.setState({ name: '' });
        if(!email) this.setState({ email: '' });
        return !!(name && email);
    }

    _validateEdit() {
        const { name } = this.state;
        if(!name) this.setState({ name: '' });
        return !!(name);
    }

    data() {
        StorageHealthUnits.getHealthUnitData(this.props.unitCurrent.id)
            .then(resp => {
                const params = {
                    ...resp,
                    symptomatic: {
                        value: resp.symptomatic,
                        label: resp.symptomatic ? 'Unidade Sintomática' : 'Unidade Assintomática'
                    }
                };
                this.setState({ ...this.state, ...params });
            })
            .catch(err => console.error(err));
    }

    edit() {
        if(!this._validateEdit()) {
            return;
        }
        this.setState({ isSaving: true });
        let params = { ...this.state, symptomatic: Number(this.state.symptomatic?.value) };
        StorageHealthUnits.editHealthUnity(this.props.unitCurrent.id, params)
            .then(() => {
                this._alert.success('Unidade editada!', () => {
                    this.props.refresh();
                    $('#modalUnits').modal('hide')
                });
            })
            .finally(() =>  this.setState({ isSaving: false }));
    }

    register() {
        if(!this._validateRegister()) {
            return;
        }
        this.setState({ isSaving: true });
        const params = { ...this.state, symptomatic: this.state.symptomatic?.value };
        StorageHealthUnits.addHealthUnity(params)
            .then(() => {
                this._alert.success(`Unidade cadastrada! Sua senha de acesso foi enviada para ${this.state.email}`, () => {
                    this.props.refresh();
                    $('#modalUnits').modal('hide')
                });
            })
            .catch(err => {
                if(err === 409) this._alert.warning('Email ja cadastrado!');
            })
            .finally(() =>  this.setState({ isSaving: false }));
    }

    componentDidMount() {
        $('#modalUnits')
            .on('hidden.bs.modal', () => {
                this.setState({ ...INITIAL_STATE });
                this.setState({ clear: !this.state.clear });
            })
            .on('shown.bs.modal', () => {
                if(this.props.isEdit) this.data();
            });
    }

    render() {
        const { isEdit } = this.props;
        const { name, email, address, city, symptomatic, telephone, isSaving, clear } = this.state;
        return(
            <div className="modal fade" id="modalUnits" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                            <h5 className="modal-title" id="exampleModalLabel">
                                Registrar unidade
                            </h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <fieldset>
                                <form>
                                    <Row>
                                        <div
                                            className={
                                                isEdit
                                                    ? "col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3"
                                                    : "col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3"
                                            }
                                        >
                                            <label>Nome</label>
                                            <input
                                                id="name"
                                                type="text"
                                                value={name ?? ''}
                                                placeholder="Nome"
                                                onChange={this._onChange.bind(this)}
                                                className={`form-control ${name === '' && 'is-invalid'}`}
                                            />
                                            <div className="invalid-feedback">
                                                Digite o nome da unidade.
                                            </div>
                                        </div>
                                        {
                                            !isEdit &&
                                                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                    <label>E-mail do administrador</label>
                                                    <input
                                                        id="email"
                                                        type="email"
                                                        value={email ?? ''}
                                                        placeholder="E-mail"
                                                        onChange={this._onChange.bind(this)}
                                                        className={`form-control ${email === '' && 'is-invalid'}`}
                                                    />
                                                    <div className="invalid-feedback">
                                                        Digite o email.
                                                    </div>
                                                </div>
                                        }
                                    </Row>
                                    <Row>
                                        <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                            <label>Telefone</label>
                                            <div className="input-group">
                                                <input
                                                    id="telephone"
                                                    type='text'
                                                    value={telephone ?? ''}
                                                    placeholder="Telefone"
                                                    className="form-control"
                                                    onChange={this._onChange.bind(this)}
                                                />
                                            </div>
                                        </div>
                                        <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                            <label>Tipo</label>
                                            <Select
                                                isLoading={false}
                                                isClearable={true}
                                                value={symptomatic ?? ''}
                                                options={this._optionsUnit}
                                                placeholder='Selecione o tipo da Unidade'
                                                onChange={event => this.setState({ symptomatic: event })}
                                            />
                                        </div>
                                    </Row>
                                    <Row>
                                        <div className="col-sm-12 col-md-12 col-lg-9 col-xs-9 mb-3">
                                            <label>Endereço</label>
                                            <Search
                                                clear={clear}
                                                address={address}
                                                className="form-control"
                                                onChange={this._onChange.bind(this)}
                                                onEvent={this._onChangeLatLong.bind(this)}
                                            />
                                        </div>
                                        <div className="col-sm-12 col-md-12 col-lg-3 col-xs-3 mb-3">
                                            <label>Cidade</label>
                                            <input
                                                id='city'
                                                type="text"
                                                value={city ?? ''}
                                                placeholder="Cidade"
                                                className="form-control"
                                                onChange={this._onChange.bind(this)}
                                            />
                                        </div>
                                    </Row>
                                </form>
                            </fieldset>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-dark"
                                onClick={() => isEdit ? this.edit() : this.register()}
                            >
                                {isSaving ? <Loader color='white' size={12} /> : 'Salvar' }
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

