import { Fila } from '../../../factory/request/fila';
import { RequestHistoryRead } from '../../../factory/request/request';
/**
 * @author Hector Rodrigues Da Silva
 * @class StorageHistory
 */
export class StorageHistory {
    /**
    *
    * @return {Promise<any>}
    */
    static getHistory() {
        return new Promise((resolve, reject) =>
            new Fila().run(new RequestHistoryRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status)));
    }

    static prepareHistory(logs) {
        let dates = [], reduced = [];
        logs.map(log => dates.push({"date": String(log.date.substring(0, 10)), "logs": []}));
        dates.forEach(item => {
            let duplicated  = reduced.findIndex(redItem => {
                return item.date === redItem.date;
            }) > -1;
            if(!duplicated) reduced.push(item);
        });
        logs.map(log => {
            reduced.map(date => {
                if(String(log.date.substring(0, 10)) === date.date) date.logs.push(log);
            });
        });
        return reduced;
    }

    static getIcon(key) {
        switch (key) {
            case 'death_patient':
                return 'head-side-cough-slash';
            case 'register_patient':
                return 'list-alt';
            case 'delete_patient':
                return 'user-slash';
            case 'exam_patient':
                return 'diagnoses';
            case 'phonecall_completed':
                return 'phone-alt';
            case 'update_patient':
                return 'user-edit';
            default:
                return 'exclamation';
        }
    }
}
