import React, { Fragment } from 'react';
import { Row, Box } from '../../../helpers';
import Select from 'react-select';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const Family = ({ onChangeOptionFamily, onCreateBoxFamily, familyPatient, removeBoxFamily, isCreateFamily, onChangeBoxFamily, isFamily, isEdit }) =>
    <>
        <Row>
            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                <label>Paciente contém convivência com familiares ou pessoas comuns?</label>
                <Select
                    isDisabled={isFamily}
                    options={[
                        { id: "family_create", value: 'Sim', label: 'Sim' },
                        { id: "family_create", value: 'Não', label: 'Não' }
                    ]}
                    placeholder={'Ver opções'}
                    onChange={onChangeOptionFamily}
                />
            </div>
            {isCreateFamily &&
                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mt-3 mb-3">
                    <center>
                        <button
                            type="button"
                            className="btn btn-outline-primary"
                            onClick={onCreateBoxFamily}
                        >
                            <FontAwesomeIcon icon="plus" />
                        &nbsp;Adicionar mais membros de convívio
                    </button>
                    </center>
                </div>
            }
        </Row>
        {isCreateFamily &&
            <div style={{ height: 600, overflowY: 'auto', boxShadow: 'inset 0 0 8px #b4b4b4', padding: 20 }}>
                <Row>
                    {familyPatient.length > 0 && familyPatient.map((item, index) =>
                        <Fragment key={index}>
                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                <Box
                                    styleBox={{ border: '1px solid #dedede', borderRadius: 7 }}
                                    styleHeader={{ borderTop: '3px solid #00b88a', borderTopLeftRadius: 7, borderTopRightRadius: 7 }}
                                    titleHeader={
                                        <Row>
                                            <div className="col-sm-11 col-md-11 col-lg-11 col-xs-11">
                                                <label><strong>Pessoa {index + 1}:</strong></label>
                                            </div>
                                            <div className="col-sm-1 col-md-1 col-lg-1 col-xs-1">
                                                <span
                                                    style={{ color: 'gray', cursor: 'pointer' }}
                                                    onClick={event => removeBoxFamily(event, index)}>
                                                    <FontAwesomeIcon icon="times-circle" />
                                                </span>
                                            </div>
                                        </Row>
                                    }>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                                        <label>Nome completo:</label>
                                        <input
                                            type="text"
                                            name="name"
                                            className={`form-control`}
                                            value={item.name}
                                            placeholder="Nome"
                                            onChange={event => onChangeBoxFamily(event, index)}
                                        />
                                    </div>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                                        <label>Estado:</label>
                                        <Select
                                            menuPortalTarget={document.body}
                                            styles={{
                                                menuPortal: styles => ({ ...styles, zIndex: 5000 })
                                            }}
                                            value={isEdit ? {
                                                label: item.symptomatic == 'Assintomático' && 'Assintomático'
                                                    || item.symptomatic == 'Sintomático' && 'Sintomático'
                                                    || 'Nennuma opção selecionada',
                                                value: item.symptomatic == 'Assintomático' && 'Assintomático'
                                                    || item.symptomatic == 'Sintomático' && 'Sintomático'
                                                    || 'Nennuma opção selecionada'
                                            } :
                                            {
                                                label: !isEdit && !item.symptomatic ? 'Nenhuma opção selecionada' : item.symptomatic,
                                                value: !isEdit && !item.symptomatic ? null : item.symptomatic
                                            }}
                                            options={[
                                                { id: "symptomatic", value: 'Sintomático', label: 'Sintomático' },
                                                { id: "symptomatic", value: 'Assintomático', label: 'Assintomático' }
                                            ]}
                                            placeholder={'Ver opções'}
                                            onChange={event => onChangeBoxFamily(event, index)}
                                        />
                                    </div>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                                        <label>Exame:</label>
                                        <Select
                                            menuPortalTarget={document.body}
                                            styles={{
                                                menuPortal: styles => ({ ...styles, zIndex: 5000 })
                                            }}
                                            value={{
                                                label: item.exam_result == 'Positivo' && 'Positivo'
                                                    || item.exam_result == 'Negativo' && 'Negativo'
                                                    || item.exam_result == 'Aguardando Resultado' && 'Aguardando Resultado' || 'Nenhuma opção selecionada',
                                                value:
                                                    item.exam_result == 'Negativo' && 'Negativo'
                                                    || item.exam_result == 'Positivo' && 'Positivo'
                                                    || item.exam_result == 'Aguardando Resultado' && 'Aguardando Resultado' || 'Nenhuma opção selecionada',
                                            }}
                                            options={[
                                                { id: "exam_result", value: 'Positivo', label: 'Positivo' },
                                                { id: "exam_result", value: 'Negativo', label: 'Negativo' },
                                                { id: "exam_result", value: 'Aguardando Resultado', label: 'Aguardando Resultado' }
                                            ]}
                                            placeholder={'Ver opções'}
                                            onChange={event => onChangeBoxFamily(event, index)}
                                        />
                                    </div>
                                    <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                                        <label>Tipo de membro:</label>
                                        <Select
                                            menuPortalTarget={document.body}
                                            styles={{
                                                menuPortal: styles => ({ ...styles, zIndex: 5000 })
                                            }}
                                            value={{
                                                label: item.kinship || 'Nenhuma opção selecionada',
                                                value: item.kinship || null
                                            }}
                                            options={[
                                                { id: "kinship", value: 'Marido', label: 'Marido' },
                                                { id: "kinship", value: 'Esposa', label: 'Eposa' },
                                                { id: "kinship", value: 'Pai', label: 'Pai' },
                                                { id: "kinship", value: 'Mãe', label: 'Mãe' },
                                                { id: "kinship", value: 'Irmão(a)', label: 'Irmão(a)' },
                                                { id: "kinship", value: 'Avô(a)', label: 'Avô(a)' },
                                                { id: "kinship", value: 'Amigo(a)', label: 'Amigo(a)' },
                                                { id: "kinship", value: 'Colega de trabalho', label: 'Colega de trabalho' },
                                                { id: "kinship", value: 'Outro', label: 'Outro' }
                                            ]}
                                            placeholder={'Ver opções'}
                                            onChange={event => onChangeBoxFamily(event, index)}
                                        />
                                    </div>
                                </Box>
                            </div>
                        </Fragment>
                    )}
                </Row>
            </div>}
    </>;

export default Family;