import React from 'react';
import { Row, Loader } from '../../helpers';

export const RegisterHelp = ({ onChangeValues, submitVideo, isLoadButton, isEdit, submitEditVideo, isClassErrorsValues, isLoadEdit, value }) =>
    <div className="modal fade" id="register_help">
        <div className="modal-dialog">
            <div className="modal-content">
                <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                    <h5 className="modal-title">{!isEdit ? 'Registrar vídeo do YouTube' : 'Editar vídeo'}</h5>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    {isLoadEdit ?
                        <center>
                            <Loader size={15} />
                        </center>
                        :
                        <fieldset>
                            <form>
                                {!isEdit &&
                                <Row>
                                    <div className="form-group col-md-12">
                                        <label>URL:</label>
                                        <input type="text" value={value.hash} className={`form-control ${isClassErrorsValues.hash}`} id="hash" onChange={event => onChangeValues(event)} placeholder="URL do vídeo" />
                                        <div className="invalid-feedback">
                                            Digite a URL.
                                        </div>
                                    </div>
                                </Row>
                                }
                                <Row>
                                    <div className="form-group col-md-12">
                                        <label>Título:</label>
                                        <input type="text" value={value.title} className={`form-control ${isClassErrorsValues.title}`} id="title" onChange={event => onChangeValues(event)} placeholder="Título do vídeo" />
                                        <div className="invalid-feedback">
                                            Digite um título.
                                        </div>
                                    </div>
                                </Row>
                                <Row>
                                    <div className="form-group col-md-12">
                                        <label>Para qual página específica?</label>
                                        <input type="text" value={value.type} className={`form-control ${isClassErrorsValues.type}`} id="type" onChange={event => onChangeValues(event)} placeholder="Nome da página" />
                                        <div className="invalid-feedback">
                                            Digite o nome da página do tutorial.
                                    </div>
                                    </div>
                                </Row>
                            </form>
                        </fieldset>
                    }
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
                    <button onClick={!isEdit ? submitVideo : submitEditVideo} type="button" className="btn btn-primary" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                        {!isLoadButton ? 'Salvar' : <Loader size={10} color={'white'} />}
                    </button>
                </div>
            </div>
        </div>
    </div>;

export default RegisterHelp