import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestHelpUpdate extends Request {

    constructor(param, idHelp) {
        const url = `${Routes.HELP}/${idHelp}`;
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        super(url, "PUT", headers, param);
    }

}
