import './index.css';

/** @name Dependencies */
import React from 'react';
import $ from 'jquery';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

/** @name External */
import { Loader } from '../../../../helpers';

/**
 * @author Gustavo
 * @helper boxCodeRecovery helper
 */
export const BoxCodeRecovery = ({ nextRecovery, backRecovery, onChangeCode, load, refreshCode, alertError }) =>
    <div className='container__boxCode'>
        <button className={'button__back__boxCode'} onClick={backRecovery}>
            <FontAwesomeIcon icon="arrow-left" style={{ cursor: 'pointer' }} size={'2x'} />
        </button>
        <div className='container__full'>
            <div className='header__boxCode'>
                <div className='icon__custom__boxCode'>
                    <FontAwesomeIcon icon="envelope-open-text" color={'white'} />
                </div>
                <h1 className='text__title__boxCode'>
                    Digite o Código
                </h1>
            </div>
            <h2 className='text__subtitle__boxCode'>
                Estamos quase lá! Agora digite abaixo o código que enviamos no seu E-mail.
            </h2>
            <div className={`alert alert-${alertError.alert} alert-dismissible show mt-3 alert__${alertError.className == "code" && alertError.className}`} role="alert">
                <strong>{alertError.title}</strong>&nbsp;{alertError.message}
                <button onClick={() => $('.alert').fadeOut('slow')} type="button" className="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className='container__card__code'>
                <div className='container__input__boxCode'>
                    <input
                        type="text"
                        maxLength='6'
                        className='input__boxCode'
                        onChange={event => onChangeCode(event.target.value)}
                        onKeyPress={event => event.key === 'Enter' && nextRecovery()}
                    />
                </div>
            </div>
            <button className='button__boxCode' onClick={nextRecovery}>
                {
                    load
                        ? <Loader color={'white'} size={10} />
                        : <h1 className='label__button__boxCode'>VALIDAR CÓDIGO</h1>
                }
            </button>
            <button className='recovery__text__boxCode' onClick={refreshCode}>
                Reenviar Código
            </button>
        </div>
    </div>;
