import { Fila } from '../../../factory/request/fila';
import { RequestUserTransUnity } from '../../../factory/request/request';
/**
 * @author Gustavo
 * @class StoragePhoneCalls
 */
export class StorageChangeUnity {

    static transPatientUnity( idUnity ) {
        let unityObj={
            health_unit : Number(idUnity)
        };
        return new Promise ((resolve, reject) => {
            new Fila().run(new RequestUserTransUnity(unityObj))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        })
    }
}
