import 'moment-timezone';
import 'moment/locale/pt-br';
import moment from 'moment';

moment.tz('America/Sao_Paulo');
/**
 * @author Giovane Santos Silva
 * @class Moment
 */
export class MomentCustom {
    /**
     * get moment
     *
     * @param {Date} data
     * @param locale
     * @return {Object} moment
     */
    static getMoment(data = new Date(), locale='pt-br') {
        return moment(data).locale(locale);
    }

    /**
     * get custom date format
     *
     * @param custom
     * @param locale
     * @param data
     * @return {string}
     */
    static formatCustom(data, custom, locale='pt-br') {
        return moment(data).locale(locale).format(custom);
    }

    /**
     *
     * @param data
     * @param locale
     * @return {string}
     */
    static fromNow(data, locale='pt-br') {
        return moment(data).locale(locale).fromNow();
    }

}

export default MomentCustom;
