import React, { useEffect, useState } from 'react';
import ReactTooltip from "react-tooltip";
import { Table } from "../../helpers/table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const List = ({ data, onDelete, onEdit, onTransfer, logged, isData }) => {

    const [ size, setSize ] = useState(window.innerWidth);
    useEffect(() => {
        const resize = () => setSize(window.innerWidth);
        window.addEventListener('resize', resize);
        return function cleanup () {
            window.removeEventListener('resize', resize);
        }
    }, []);

    const hideActions = size <= 930;
    const hideMobile = size <= 640;

    const columns = [{
        dataField: 'id',
        text: 'ID',
        sort: true,
        hidden: true
    }, {
        dataField: 'name',
        text: 'Nome',
        sort: true,
        hidden: hideMobile
    }, {
        dataField: 'email',
        text: 'Email',
        sort: true,
    }, {
        dataField: 'office',
        text: 'Cargo',
        sort: true,
        hidden: hideMobile
    },{
        dataField: 'type',
        text: 'Tipo',
        sort: true,
        formatter: typeFormat
    }, {
        dataField: '',
        text: 'Ações',
        align: 'center',
        headerAlign: 'center',
        formatter: actionFormat,
        hidden: hideActions
    }];

    function typeFormat(cell) {
        return(
            <span style={{ textTransform: 'capitalize' }}>{cell}</span>
        );
    }

    function actionFormat(cell, row) {
        return (
            <>
                <button
                    data-for='edit'
                    data-tip='Editar'
                    onClick={() => onEdit(row)}
                    className='btn btn-sm btn-secondary'
                >
                    <FontAwesomeIcon size='xs' icon='pencil-alt' color='white' />
                </button>
                <ReactTooltip id='edit' place="top" type="dark" effect="solid" />
                <button
                    style={{ marginLeft: 5 }}
                    data-for='transfer'
                    data-tip='Transferir'
                    onClick={() => onTransfer(row)}
                    className='btn btn-sm btn-dark'
                    disabled={row.id === logged.id || logged.type !== 'admin'}
                >
                    <FontAwesomeIcon size='xs' icon='exchange-alt' color='white' />
                </button>
                <ReactTooltip id='transfer' place="top" type="dark" effect="solid" />
                <button
                    style={{ marginLeft: 5 }}
                    data-for='delete'
                    data-tip='Excluir'
                    className='btn btn-sm btn-danger'
                    onClick={() => onDelete(row)}
                    disabled={row.id === logged.id || logged.type !== 'admin'}
                >
                    <FontAwesomeIcon icon='times' color='white' />
                </button>
                <ReactTooltip id='delete' place="top" type="dark" effect="solid" />
            </>
        );
    }

    return(
        <Table
            keyField='id'
            data={data}
            isData={isData}
            columns={columns}
        />
    );
};
