export const FilterList = (list, filter, ...attr) => {
    let newList = [];
    if (!filter) return list;
    if(attr.length <= 0) attr.push('name');
    attr.forEach(att => {
        let filterList = list.filter(item =>
            item[att].toLowerCase().indexOf(filter.toLowerCase()) >= 0
        );
        filterList.forEach(item => newList.push(item));
    });
    return [...new Set(newList)];
};
