import $ from "jquery";
import React from 'react';
import Select from 'react-select';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { StorageUsers } from "./storage";

import { Alert } from "../../factory/alert";
import { Row, Loader } from '../../helpers';
import { AsyncStorage } from "../../factory/storage";
import {Platform} from "../../services/platform";

const INITIL_STATE = {
    name: null,
    email: null,
    office: null,
    type: null,
    password: null,
    cpassword: null,
    isSaving: false,
    isLoadEdit: false,
    showPassword: false
};

export class Register extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            ...INITIL_STATE
        };
        this._optionsType = [
            { value: 2, label: 'Monitoramento' },
            { value: 3, label: 'Municipal' },
        ];
        this._alert = new Alert();
        this._storage = AsyncStorage.getObject('user');
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
        }
    }

    _onChange(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    _onChangeShowPassword() {
        this.setState({ showPassword: !this.state.showPassword });
    }

    _validateRegister() {
        const { name, email, password, cpassword } = this.state;
        if (!name) this.setState({ name: '' });
        if (!email) this.setState({ email: '' });
        if (!password) this.setState({ password: '' });
        if (password !== cpassword) this.setState({ cpassword: '' });
        return !!(name && email && password && password === cpassword);
    }

    _validateEdit() {
        const { name, email } = this.state;
        if (!name) this.setState({ name: '' });
        if (!email) this.setState({ email: '' });
        return !!(name && email);
    }

    data() {
        StorageUsers.getData(this.props.userCurrent.id)
            .then(resp => {
                this.setState({
                    name: resp.name,
                    office: resp.office,
                    email: resp.email,
                    type: resp.type
                }, () => this.setState({ isLoadEdit: false }));
            })
            .catch(err => this._errStatusCode(err));
    }

    edit() {
        if (!this._validateEdit()) {
            return;
        }
        this.setState({ isSaving: true });
        const params = { ...this.state, type: this.state.type?.value };
        StorageUsers.editUser(this.props.userCurrent.id, params)
            .then(() => {
                this._alert.success('Usuário editado!', () => {
                    this.props.refresh();
                    $('#registerModal').modal('hide');
                });
            })
            .catch(err => this._errStatusCode(err))
            .finally(() => this.setState({ isSaving: false }));
    }

    register() {
        if (!this._validateRegister()) {
            return;
        }
        this.setState({ isSaving: true });
        const params = { ...this.state, type: this.state.type?.value };
        StorageUsers.addUser(params)
            .then(() => {
                this._alert.success('Usuário cadastrado!', () => {
                    this.props.refresh();
                    $('#registerModal').modal('hide');
                });
            })
            .catch(err => {
                if (err === 409) this._alert.warning('Usuario ja cadastrado!');
                this._errStatusCode(err);
            })
            .finally(() => this.setState({ isSaving: false }));
    }

    componentDidMount() {
        if (this._storage.user.super_admin) {
            this._optionsType.unshift({ value: 1, label: 'Administrador' });
        }
        $('#registerModal')
            .on('hidden.bs.modal', () => {
                this.setState({ ...INITIL_STATE });
            })
            .on('show.bs.modal', () => {
                if (this.props.userEdit) {
                    this.setState({ isLoadEdit: true });
                    this.data();
                }
            });
    }

    render() {
        const { name, email, office, type, password, cpassword, showPassword, isSaving } = this.state;
        return (
            <div className="modal fade" id="registerModal" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                            <h5 className="modal-title">
                                {this.props.userEdit ? 'Editar usuário' : 'Adicionar usuário'}
                            </h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            {this.state.isLoadEdit
                                ? <center><Loader size={15} /></center>
                                :
                                <fieldset>
                                    <form>
                                        <Row>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Nome</label>
                                                <input
                                                    id="name"
                                                    type="text"
                                                    value={name ?? ''}
                                                    placeholder="Nome"
                                                    onChange={this._onChange.bind(this)}
                                                    className={`form-control ${name === '' && 'is-invalid'}`}
                                                />
                                                <div className="invalid-feedback">
                                                    Digite seu nome.
                                            </div>
                                            </div>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Email</label>
                                                <input
                                                    id="email"
                                                    type="email"
                                                    value={email ?? ''}
                                                    placeholder="Email"
                                                    onChange={this._onChange.bind(this)}
                                                    className={`form-control ${email === '' && 'is-invalid'}`}
                                                />
                                                <div className="invalid-feedback">
                                                    Digite seu melhor email.
                                            </div>
                                            </div>
                                        </Row>
                                        <Row>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <label>Cargo</label>
                                                <input
                                                    id="office"
                                                    type="text"
                                                    value={office ?? ''}
                                                    placeholder="Cargo"
                                                    className="form-control"
                                                    onChange={this._onChange.bind(this)}
                                                />
                                            </div>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                {!this.props.userEdit &&
                                                    <>
                                                        <label>Tipo de usuario</label>
                                                        <Select
                                                            value={type}
                                                            isLoading={false}
                                                            isClearable={true}
                                                            options={this._optionsType}
                                                            placeholder='Selecione o tipo de usuário'
                                                            onChange={event => this.setState({ type: event })}
                                                        />
                                                    </>
                                                }
                                                {this.props.userEdit &&
                                                    <>
                                                        <label>Tipo de usuario</label>
                                                        <input
                                                            readOnly
                                                            type="text"
                                                            value={type ?? ''}
                                                            placeholder="Cargo"
                                                            className="form-control"
                                                            style={{ textTransform: 'capitalize' }}
                                                        />
                                                    </>
                                                }
                                            </div>
                                        </Row>
                                        {!this.props.userEdit &&
                                            <Row>
                                                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                    <label>Password</label>
                                                    <div className="input-group">
                                                        <input
                                                            id="password"
                                                            value={password ?? ''}
                                                            placeholder="Senha"
                                                            onChange={this._onChange.bind(this)}
                                                            type={!showPassword ? 'password' : 'text'}
                                                            className={`form-control ${password === '' && 'is-invalid'}`}
                                                        />
                                                        <div className="input-group-append">
                                                            <button
                                                                type="button"
                                                                className="btn btn-outline-secondary"
                                                                onClick={this._onChangeShowPassword.bind(this)}
                                                            >
                                                                <FontAwesomeIcon icon={!showPassword ? 'eye' : 'eye-slash'} />
                                                            </button>
                                                        </div>
                                                        <div className="invalid-feedback">
                                                            Digite uma senha.
                                                    </div>
                                                    </div>
                                                </div>
                                                <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                    <label>Confirm Password</label>
                                                    <input
                                                        id="cpassword"
                                                        value={cpassword ?? ''}
                                                        placeholder="Confirmar Senha"
                                                        onChange={this._onChange.bind(this)}
                                                        type={!showPassword ? 'password' : 'text'}
                                                        className={`form-control ${cpassword === '' && 'is-invalid'}`}
                                                    />
                                                    <div className="invalid-feedback">
                                                        As senhas nao coecidem.
                                                </div>
                                                </div>
                                            </Row>
                                        }
                                    </form>
                                </fieldset>
                            }
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                disabled={isSaving}
                                className="btn btn-dark"
                                onClick={() => this.props.userEdit ? this.edit() : this.register()}
                            >
                                {isSaving ? <Loader size={8} color={'white'} /> : 'Salvar'}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
