import { Request } from '../request';
import env from "../../../constants/.env";
import Routes from '../../../constants/routes';

export class RequestRecoveryRequest extends Request {

    constructor(email) {
        const url = `${Routes.RECOVERY}/request`;
        const headers = { 
            'Authorization': env.AUTHORIZATION_RECOVERY,
            'Content-Type': 'application/x-www-form-urlencoded'
         };
        const params = { email };
        super(url, "POST", headers, params);
    }

}