import { Fila } from '../../../factory/request/fila';
import { RequestHealthUnitRead, RequestHealthUnityAdd, RequestHealthUnitDelete, RequestHealthUnitUpdate, RequestHealthUnitData } from '../../../factory/request/request';

/**
 * @author Gustavo
 * @class StorageHealthUnits
 */
export class StorageHealthUnits {

    static getHealthUnits() {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnitRead())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        })
    }

    static getHealthUnitData(id) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnitData(id))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        })
    }

    static addHealthUnity(unity) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnityAdd(unity))
                .then(res => resolve(res.data))
                .catch(err => reject(err.response.status))
        })
    }

    static editHealthUnity(id, unity) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnitUpdate(id, unity))
                .then(res => resolve(res))
                .catch(err => reject(err))
        })
    }

    static deleteHealth(id) {
        return new Promise((resolve, reject) => {
            new Fila().run(new RequestHealthUnitDelete(id))
                .then(res => resolve(res.data))
                .catch(err => reject(err))
        })
    }
}
