import React, { useEffect, useState } from 'react';
import Moment from "moment";
import ReactTooltip from "react-tooltip";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { Table } from "../../helpers/table";

export const List = ({ data, isData, onRegister, onView, logged }) => {

    const [ size, setSize ] = useState(window.innerWidth);
    useEffect(() => {
        const resize = () => setSize(window.innerWidth);
        window.addEventListener('resize', resize);
        return function cleanup () {
            window.removeEventListener('resize', resize);
        }
    }, []);

    const hideCity = size <= 1366;
    const hideObservations = size <= 1352;
    const hideAddress = size <= 930;
    const hideMobile = size <= 640;

    const columns = [{
        dataField: 'id',
        text: 'ID',
        sort: true,
        hidden: true
    }, {
        dataField: 'name',
        text: 'Nome',
        sort: true,
    }, {
        dataField: 'address',
        text: 'Endereço',
        sort: true,
        hidden: hideAddress || hideCity
    }, {
        dataField: 'city',
        text: 'Cidade',
        sort: true,
        hidden: hideCity
    }, {
        dataField: 'telephone',
        text: 'Telefone',
        sort: true,
    }, {
        dataField: 'description',
        text: 'Observações',
        sort: true,
        hidden: hideObservations,
        headerStyle: (colum, colIndex) => {
            return { width: '200px', textAlign: 'center' };
        }
    }, {
        headerAlign: 'center',
        text: 'Data',
        sort: true,
        align: 'center',
        dataField: 'date',
        formatter: dataFormat,
        hidden: hideMobile,
    }, {
        sort: true,
        text: 'Finalizado',
        align: 'center',
        headerAlign: 'center',
        dataField: 'completed_in',
        formatter: finalizedFormat,
        hidden: hideMobile,
    }, {
        sort: true,
        text: 'Status',
        align: 'center',
        dataField: 'status',
        headerAlign: 'center',
        formatter: statusFormater,
    }, {
        dataField: '',
        text: 'Ações',
        align: 'center',
        headerAlign: 'center',
        formatter: actionFormater,
        hidden: hideMobile,
    }];

    function dataFormat(cel, row) {
        return(
            <>{Moment(row.date).format('DD/MM/YYYY hh:mm')}</>
        )
    }

    function finalizedFormat(cel, row) {
        return(
            <>{row.completed_in === null ? ' - ' : Moment(row.completed_in).format('DD/MM/YYYY hh:mm')}</>
        )
    }

    function statusFormater(cel, row) {
        return (
            <FontAwesomeIcon icon={row.status === 'pendent' ? 'times' : 'check'} color={row.status === 'pendent' ? 'red' : '#1da584'} />
        )
    }

    function  actionFormater(cell, row) {
        return (
            <>
                {row.status === "completed" ?
                    <>
                        <button
                            data-for="call"
                            data-tip='Dados da ligação'
                            className='btn btn-sm btn-primary'
                            onClick={() => onView(row)}
                        >
                            <FontAwesomeIcon icon='file-alt' color='white' />
                        </button>
                        <ReactTooltip id='call' place="top" type="dark" effect="solid" />
                    </>
                :
                    <>
                        <button
                            data-for="register"
                            data-tip='Registrar ligação'
                            className="btn btn-sm btn-success"
                            disabled={logged.type === "municipal"}
                            onClick={() => onRegister(row)}
                        >
                            <FontAwesomeIcon size='xs' icon='phone-alt' color='white' />
                        </button>
                        <ReactTooltip id='register' place="top" type="dark" effect="solid" />
                    </>
                }
            </>
        );
    }

    return(
        <Table
            data={data}
            isData={isData}
            columns={columns}
            keyField='id_phone_call'
        />
    );

};
