import './css/seeFamily.css';
import React, { Fragment } from 'react';
import { Row, Box, Loader, Line } from '../../helpers';

export const SeeFamily = ({ familyPatient, isLoadSeeFamily }) =>
    <div className="modal fade" id="see_family" aria-hidden="true">
        <div className="modal-dialog modal-lg">
            <div className="modal-content">
                <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                    <h5 className="modal-title">{familyPatient.length > 0 ? `${familyPatient.length === 1 ?
                        `${familyPatient.length} Familiar do paciente` : `${familyPatient.length} Familiares do paciente`}` : 'Familiares do paciente'}</h5>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    <div className="container__family">
                        <Row>
                            {isLoadSeeFamily
                                ? 
                                <center>
                                    <Loader size={15} />
                                </center>
                                :
                                familyPatient.length <= 0
                                    ? 
                                    <center>
                                        Nenhum familiar encontrado.
                                    </center>
                                    :
                                    familyPatient.map((item, index) =>
                                        <Fragment key={index}>
                                            <div className="col-sm-12 col-md-12 col-lg-6 col-xs-6 mb-3">
                                                <Box
                                                    styleBox={{ border: '1px solid #dedede', borderRadius: 7, margin: 0 }}
                                                    styleHeader={{ borderTop: '3px solid #00b88a', borderTopLeftRadius: 7, borderTopRightRadius: 7 }}>
                                                    <Row>
                                                        <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12">
                                                            <label style={{ textTransform: 'capitalize' }}><strong>Nome:</strong>&nbsp;{item.name}</label>
                                                        </div>
                                                    </Row>
                                                    <div className="content__card__family">
                                                        <span>
                                                            <strong>Estado:</strong> <>{item.symptomatic ? 'Sintomático' : 'Assintomático'}</>
                                                        </span>
                                                        <Line />
                                                        <br/>
                                                        <span>
                                                            <strong>Exame:</strong> <>{item.exam_result}</>
                                                        </span>
                                                        <Line />
                                                        <br/>
                                                        <span>
                                                            <strong>Tipo de membro:</strong> <>{item.kinship}</>
                                                        </span>
                                                        <Line />
                                                    </div>
                                                </Box>
                                            </div>
                                        </Fragment>
                                    )}
                        </Row>
                    </div>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
                </div>
            </div>
        </div>
    </div>;
