import { Fila } from '../../../factory/request/fila';
import RequestExecutor from '../../../factory/request/executor';
import { RequestToken, RequestUserLogged, RequestRecoveryRequest, RequestRecoveryConfirm, RequestRecoveryPassword } from '../../../factory/request/request';
/**
 * @author Gustavo
 * @class StorageLogin
 */
export class StorageLogin {

    static authToken(email, password) {
        return new Promise((resolve, reject) => {
            RequestExecutor(new RequestToken(email, password))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static getUser(){
        return new Promise ((resolve, reject) => {
            new Fila().run(new RequestUserLogged())
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        })
    }

    static recoveryEmail(email){
        return new Promise((resolve, reject) => {
            RequestExecutor(new RequestRecoveryRequest(email))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static recoveryPass(password, code){
        return new Promise((resolve, reject) => {
            RequestExecutor(new RequestRecoveryPassword(password, code))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static recoveryCode(code){
        return new Promise((resolve, reject) => {
            RequestExecutor(new RequestRecoveryConfirm(code))
                .then(resp => resolve(resp.data))
                .catch(err => reject(err.response.status));
        });
    }

    static scrollCarousel(scroll) {
        switch (scroll) {
            case 0:
                return 'login';
            case 1:
                return 'recoveryEmail';
            case 2:
                return 'recoveryCode';
            default:
                return 'recoveryPassword';
        }
    }
}
