import './index.css';
import swal from 'sweetalert';
/**
 * @author Hector Rodrigues Da Silva
 * @class Alert
 */
export class Alert {
    /**
     *
     * @param options
     * @param callback
     */
    custom(options, callback) {
        swal(options).then(value => callback(value));
    }

    /**
     *
     * @param message
     * @param callback
     */
    fixed(message, callback) {
        swal({
            button: false,
            closeOnEsc: false,
            closeOnClickOutside: false,
            text: (message)
        }).then(value => callback(value));
    }

    /**
     *
     * @param message
     * @param callback
     */
    info(message, callback = () => {}) {
        swal('Aviso', message, "info").then(() => callback());
    }

    /**
     *
     * @param title
     * @param message
     * @param callback
     */
    fullinfo(title, message, callback = () => {}) {
        swal(title, message, "info").then(() => callback());
    }

    /**
     *
     * @param message
     * @param callback
     */
    error(message, callback = () => {}) {
        swal('Erro', message, "error").then(v => callback(v));
    }

    /**
     *
     * @param message
     * @param callback
     */
    success(message, callback = () => {}) {
        swal('Sucesso', message, "success").then(() => callback());
    }

    /**
     *
     * @param message
     * @param callback
     */
    warning(message, callback = () => {}){
        swal('Aviso', message, "warning").then(() => callback());
    }

    /**
     *
     */
    close() {
        swal.close();
    }
}
