import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestUserRefreshPassword extends Request {

    constructor(id, user) {
        const url = `${Routes.USER}/${id}/password`;
        const headers = { 'Content-Type': 'application/x-www-form-urlencoded' };
        const params = { ...user };
        super(url, "PATCH", headers, params);
    }

}
