import './css/dashboard.css';
/** @name Dependencies */
import React from 'react';
import Moment from 'moment';
import CountUp from 'react-countup';
import { Link, withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name Internal */
import { DashboardStorage } from './storage';
/** @name External */
import { Loader, ChartApex } from '../../helpers';
import { ItemDash, BoxDashboard, BoxDefault, BoxCharts } from './helpers';

let optionsChart = {
    chart: { stacked: true, zoom: { enabled: true } },
    title: null,
    subtitle: { text: 'Município de Garça', align: 'left' },
    labels: [],
    xaxis: {
        labels: {
            rotate: -55,
            style: {
                colors: [],
                fontSize: '10px',
                fontFamily: 'Arial, sans-serif',
                fontWeight: 500,
                cssClass: 'apexcharts-xaxis-label',
            },
        }
    },
    colors: [],
    plotOptions: {
        bar: {
            horizontal: true,
            dataLabels: {
                position: 'top',
            },
        }
    },
    fill: {
        colors: null,
        opacity: 0.9,
        type: null,
        gradient: {
            type: null,
            shadeIntensity: 0.5,
            gradientToColors: null,
            inverseColors: true,
            opacityFrom: 1
        }
    },
};
/**
 * @author Hector Rodrigues Da Silva
 * @class Dashboard
 */
class Dashboard extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            // data analytic
            dataTotal: [],
            dataChartCasesUnit: [],
            dataChartComorbidities: [],
            dataChartDeathsUnit: [],
            dataChartPhoneCallsUnit: [],
            dataChartAvarage: [],
            // options charts
            optionsChartCasesUnit: null,
            optionsChartComorbidities: null,
            optionsChartDeathsUnit: null,
            optionsChartPhoneCallsUnit: null,
            optionsChartAvarage: null,
            // is load data
            isLoadTotal: true,
            isLoadPhoneCalls: true,
            isLoadCasesUnit: true,
            isLoadComorbidities: true,
            isLoadDeathsUnit: true,
            isLoadAvarageUnit: true,
            // internal error
            isInitialMessageLoadBox: 0,
            isInitialMessageLoadChartsCases: 'Carregando informações...',
            isInitialMessageLoadChartsPhoneCalls: 'Carregando informações...',
            isInitialMessageLoadChartsAvarage: 'Carregando informações...'
        };
        this.numberDayCurrent = Moment(new Date()).format("DD");
        this.textMonthCurrent = Moment().month(new Date().getMonth()).format("MMMM");
    }

    _setOptionsChart(horizontalGraph, typeDesignChart, title, labelsCharts, optionState, loadChartState) {
        let fillColors = [];
        let gradientFillColors = [];
        for (let i = 0; i < labelsCharts.length; i++) {
            fillColors.push('#00b88a');
            gradientFillColors.push('#00b88a');
        }
        let objectOptions = { ...optionsChart };
        if (!horizontalGraph) objectOptions.plotOptions = {};
        objectOptions.title = { text: title, align: 'left' };
        objectOptions.labels = labelsCharts;
        objectOptions.fill.colors = fillColors;
        objectOptions.fill.type = typeDesignChart;
        objectOptions.fill.gradient.gradientToColors = gradientFillColors;
        this.setState({ [optionState]: objectOptions, [loadChartState]: false });
    }

    _getAvarageAge() {
        DashboardStorage.getAvarage()
            .then(data => {
                let dataAvarage = [{ name: "Média de", data: [] }];
                let labelsAvarage = [];
                data.map(avarage => {
                    dataAvarage[0].data.push(avarage.average_age);
                    labelsAvarage.push(avarage.name);
                });
                this.setState({ dataChartAvarage: dataAvarage }, () => {
                    this._setOptionsChart(false, 'gradient', 'Média de idades por unidade', labelsAvarage, 'optionsChartAvarage', 'isLoadAvarageUnit');
                });
            })
            .catch(err => this.setState({ isInitialMessageLoadChartsAvarage: 'Gráfico indisponível' }));
    }

    _getPhoneCalls() {
        // Get data phone calls unit
        DashboardStorage.getPhoneCalls()
            .then(data => {
                let dataQuantityCallUnit = [{ name: "Ligações", data: [] }];
                let labelsCallUnit = [];
                data.map(phoneCalls => {
                    dataQuantityCallUnit[0].data.push(phoneCalls.phone_calls);
                    labelsCallUnit.push(phoneCalls.name);
                });
                this.setState({ dataChartPhoneCallsUnit: dataQuantityCallUnit }, () => {
                    this._setOptionsChart(true, 'gradient', 'Ligações de monitoramento por unidade', labelsCallUnit, 'optionsChartPhoneCallsUnit', 'isLoadPhoneCalls');
                });
            })
            .catch(err => this.setState({ isInitialMessageLoadChartsPhoneCalls: 'Gráfico indisponível' }));
    }

    _getCasesUnit() {
        // Get data cases unit
        DashboardStorage.getCases()
            .then(data => {
                let dataCasesUnit = [{ name: "Casos confirmados", data: [] }];
                let dataDeathsUnit = [{ name: "Óbitos confirmados", data: [] }];
                let dataComorbidities = [{ name: "Pacientes com comorbidades", data: [] }];
                let labelsCases = [];
                data.map(casesUnit => {
                    dataCasesUnit[0].data.push(casesUnit.monitoring);
                    dataDeathsUnit[0].data.push(casesUnit.deaths);
                    dataComorbidities[0].data.push(casesUnit.comorbidities);
                    labelsCases.push(casesUnit.name);
                });
                this.setState({ dataChartCasesUnit: dataCasesUnit, dataChartComorbidities: dataComorbidities, dataChartDeathsUnit: dataDeathsUnit }, () => {
                    this._setOptionsChart(false, 'solid', 'Casos confirmados por unidade', labelsCases, 'optionsChartCasesUnit', 'isLoadCasesUnit');
                    this._setOptionsChart(true, 'solid', 'Pacientes com comorbidades por unidade', labelsCases, 'optionsChartComorbidities', 'isLoadComorbidities');
                    this._setOptionsChart(false, 'gradient', 'Óbitos por unidade', labelsCases, 'optionsChartDeathsUnit', 'isLoadDeathsUnit');
                });
            }).catch(err => {
                console.log(err);
                this.setState({ isInitialMessageLoadChartsCases: 'Gráfico indisponível' });
            });
    }

    _getDataTotal() {
        // Get data total cases, unit...
        DashboardStorage.getTotal()
            .then(data => this.setState({ dataTotal: data, isLoadTotal: false }))
            .catch(err => this.setState({ isInitialMessageLoadBox: 'Indisponível' }));
    }

    componentDidMount() {
        this._getAvarageAge();
        this._getPhoneCalls();
        this._getCasesUnit();
        this._getDataTotal();
    }

    render() {
        const {
            isLoadTotal,
            isLoadCasesUnit,
            isLoadDeathsUnit,
            isLoadPhoneCalls,
            isLoadComorbidities,
            isLoadAvarageUnit,
            isInitialMessageLoadBox,
            isInitialMessageLoadChartsCases,
            isInitialMessageLoadChartsPhoneCalls
        } = this.state;
        return (
            <>
                <aside className="main__menu">
                    <div className="title__menu">
                        <h1 style={{ color: '#1da584', fontSize: 13 }}>COVID19</h1>
                        <h1>Painel&nbsp;<span style={{ fontWeight: 'normal' }}>Garça&nbsp;</span><FontAwesomeIcon icon={'shield-virus'} /></h1>
                    </div>
                    <Link to={'/'}>
                        <ItemDash
                            className={'icon__menu-active'}
                            isTitleText={false}
                            title={'Dashboard'}
                            icon={'chart-area'}
                            text={'dashboard'}
                            styleText={false}
                        />
                    </Link>
                    <Link to={'/units'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={false}
                            title={'Unidades'}
                            icon={'map-marked-alt'}
                            text={'Unidades'}
                            styleText={false}
                        />
                    </Link>
                    <Link to={'/about'}>
                        <ItemDash
                            className={'icon__menu-disabled'}
                            isTitleText={'?'}
                            title={'Sobre'}
                            icon={false}
                            text={'sobre'}
                            styleText={false}
                        />
                    </Link>
                    <ItemDash
                        icon={false}
                        classNameFather={'item__data'}
                        className={'icon__menu__data-disabled'}
                        text={this.textMonthCurrent}
                        isTitleText={this.numberDayCurrent}
                        title={`${this.numberDayCurrent} de ${this.textMonthCurrent}`}
                        styleText={{ color: 'rgb(248, 136, 45)' }}
                    />
                </aside>
                <main className="content__analytic">
                    <div className="boxes__total__info">
                        <BoxDashboard
                            title={'Casos finalizados'}
                            subtitle={'Óbitos confirmados'}
                            finished={
                                !isLoadTotal
                                    ? <CountUp duration={5} end={this.state.dataTotal.finished} />
                                    : isInitialMessageLoadBox
                            }
                            monitoring={
                                !isLoadTotal
                                    ? <CountUp duration={5} end={this.state.dataTotal.deaths} />
                                    : isInitialMessageLoadBox
                            }
                        />
                        <BoxDefault
                            title={'Unidades'}
                            subtitle={'Pacientes em monitoramento'}
                            finished={
                                !isLoadTotal
                                    ? <CountUp duration={5} end={this.state.dataTotal.health_units} />
                                    : isInitialMessageLoadBox
                            }
                            monitoring={
                                !isLoadTotal
                                    ? <CountUp duration={5} end={this.state.dataTotal.monitoring} />
                                    : isInitialMessageLoadBox
                            }
                        />
                    </div>
                    <BoxCharts className="fullscreen__chart">
                        {isLoadAvarageUnit ?
                            <>
                                <Loader />
                                <h1 className="msg__loading">{isInitialMessageLoadChartsPhoneCalls}</h1>
                            </>
                            :
                            <>
                                <ChartApex
                                    type={'bar'}
                                    width={'100%'}
                                    height={'100%'}
                                    id="chart_dashboard"
                                    data={this.state.dataChartAvarage}
                                    options={this.state.optionsChartAvarage}
                                />
                            </>}
                    </BoxCharts>
                    <div className="list__charts">
                        <BoxCharts>
                            {isLoadPhoneCalls ?
                                <>
                                    <Loader />
                                    <h1 className="msg__loading">{isInitialMessageLoadChartsPhoneCalls}</h1>
                                </>
                                :
                                <>
                                    <ChartApex
                                        type={'bar'}
                                        width={'100%'}
                                        height={'100%'}
                                        id="chart_dashboard"
                                        data={this.state.dataChartPhoneCallsUnit}
                                        options={this.state.optionsChartPhoneCallsUnit}
                                    />
                                </>}
                        </BoxCharts>
                        <BoxCharts>
                            {isLoadCasesUnit ?
                                <>
                                    <Loader />
                                    <h1 className="msg__loading">{isInitialMessageLoadChartsCases}</h1>
                                </>
                                :
                                <>
                                    <ChartApex
                                        id="chart_dashboard"
                                        width={'100%'}
                                        height={'100%'}
                                        type={'bar'}
                                        data={this.state.dataChartCasesUnit}
                                        options={this.state.optionsChartCasesUnit}
                                    />
                                </>}
                        </BoxCharts>
                        <BoxCharts>
                            {isLoadComorbidities ?
                                <>
                                    <Loader />
                                    <h1 className="msg__loading">{isInitialMessageLoadChartsCases}</h1>
                                </>
                                :
                                <>
                                    <ChartApex
                                        id="chart_dashboard"
                                        width={'100%'}
                                        height={'100%'}
                                        type={'bar'}
                                        data={this.state.dataChartComorbidities}
                                        options={this.state.optionsChartComorbidities}
                                    />
                                </>}
                        </BoxCharts>
                        <BoxCharts>
                            {isLoadDeathsUnit ?
                                <>
                                    <Loader />
                                    <h1 className="msg__loading">{isInitialMessageLoadChartsCases}</h1>
                                </>
                                :
                                <>
                                    <ChartApex
                                        id="chart_dashboard"
                                        width={'100%'}
                                        height={'100%'}
                                        type={'bar'}
                                        data={this.state.dataChartDeathsUnit}
                                        options={this.state.optionsChartDeathsUnit}
                                    />
                                </>}
                        </BoxCharts>
                    </div>
                </main>
            </>
        );
    }
}

export default withRouter(Dashboard);
