import './index.css';
import swal from 'sweetalert';
/**
 * @author Gustavo
 * @class AlertRedirect
 */
export class AlertRedirect {
    /**
     *
     * @param callback
     */
    custom(callback) {
        swal({
            title: "Criar novo paciente",
            text: "Confirmando esta ação você ira salvar os registros feitos até agora e será encaminhado para o cadastro de um novo paciente",
            icon: "warning",
            buttons: {
                cancel: "Cancelar",
                catch: {
                    text: "Confirmar",
                    value: true,
                    className:'color__cancel'
                },
              },
            cancelButtonColor: 'gray',
            dangerMode: true
          }).then(value => callback(value))
    }
}
