import './index.css';
/** @name Dependencies */
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/**
 * @author Hector Rodrigues Da Silva
 * @helper ItemDash
 */
export const ItemDash = ({ classNameFather, className, isTitleText, title, icon, text, styleText }) =>
    <section className={`item__menu${!classNameFather ? '' : ` ${classNameFather}`}`}>
        <div className={className} title={title}>
            <h1>{!isTitleText ? <FontAwesomeIcon icon={icon} /> : isTitleText}</h1>
        </div>
        {!styleText ? <p>{text}</p> : <p style={styleText}>{text}</p>}
    </section>