import './index.css';
import React from 'react';
import CountUp from 'react-countup';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const BoxInfoTable = ({ title, count, icon, onClick=null, selected=false }) =>
    <section
        onClick={onClick}
        style={{ opacity: selected ? .7 : 1 }}
        className="box__info__app shadow-lg"
    >
        <div className="icon__box__info__app">
            <FontAwesomeIcon icon={icon} size="3x" color={'white'} />
        </div>
        <div className="content__box__info__app">
            <span>{title}</span>
            <h1 className="count__number__box">
                <CountUp duration={4} start={0} end={count} />
            </h1>
        </div>
    </section>;

export default BoxInfoTable;
