import './app.css';
import React from 'react';
/** @name External */
import NavBar from '../../template/navbar/navbar';
import SideMenu from '../../template/sidemenu/aside';
import ChangeUnit from '../../modules/changeunit';
import { AsyncStorage } from '../../factory';
/**
 * @author Hector Rodrigues Da Silva
 * @class App
 */
const App = props => {
    const userInfo = AsyncStorage.getObject('user');
    return(
        <>
            <NavBar homeApp={true} />
            <div className="body__home">
                <SideMenu
                    userInfo={userInfo}
                />
                <main className="content__wrapper__home">
                    {userInfo.user.super_admin &&
                    <ChangeUnit />
                    }
                    {props.children}
                </main>
            </div>
        </>
    )
};

export default App;
