import './index.css';

/** @name Dependencies */
import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { CSVExport } from 'react-bootstrap-table2-toolkit';
import BootstrapTable from 'react-bootstrap-table-next/dist/react-bootstrap-table-next.min';

/** @name External */
import { Loader } from '..';

/**
* @author Gustavo
* @class Table helper
*/
const hideMobile = window.innerWidth <= 640;
const _customTotal = (from, to, size) => {
    if(!hideMobile) {
        return (
            <span className="react-bootstrap-table-pagination-total">
            Mostrando de {from} até {to} de {size} resultados.
        </span>
        );
    }
    return(<></>);
};

const paginationOption = {
    showTotal: true,
    hideSizePerPage: true,
    withFirstAndLast: true,
    alwaysShowAllBtns: true,
    hidePageListOnlyOnePage: false,
    paginationTotalRenderer: _customTotal,
    sizePerPageList: [{ text: '10', value: 10 }]

};

const { ExportCSVButton } = CSVExport;

export const Table = ({ data, isData, columns, keyField = 'id' }) =>
    <ToolkitProvider
        data={data}
        keyField='id'
        columns={columns}
        exportCSV={{ fileName: 'total.csv' }}
    >
        {({ csvProps, baseProps }) =>
            <>
                <ExportCSVButton {...csvProps}>
                    {'Exportar CSV'}&nbsp;<FontAwesomeIcon icon="file-csv" />
                </ExportCSVButton>
                <BootstrapTable
                    {...baseProps}
                    hover
                    striped
                    condensed
                    bootstrap4
                    data={data}
                    columns={columns}
                    keyField={keyField}
                    pagination={paginationFactory(paginationOption)}
                    noDataIndication={() =>
                        <div className='container__loader__table'>
                            {isData ? <Loader size={10} /> : 'Nenhum resultado encontrado!'}
                        </div>
                    }
                />
            </>
        }
    </ToolkitProvider>;
