/** @name Dependencies */
import $ from 'jquery';
import React from 'react';
/** @name External */
import { AsyncStorage } from '../../factory/storage';
import { List } from './list';
import { Register } from './register';
import { Alert } from '../../factory/alert';
import { FilterList } from '../../filters/filterTable';
import { StorageHealthUnits } from './storage';
import { Platform } from '../../services/platform';
import { HeaderApp, ModalWarn, Box, ContainerHeader, Row, GroupSearchTable } from '../../helpers';
/**
* @author Gustavo
* @class Users
*/
class healthUnit extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            isData: false,
            filter: '',
            isEdit: false,
            showModal: false,
            unitCurrent: null,
        };
        this._alert = new Alert();
        this._storage = AsyncStorage.getObject('user');
    }

    _errStatusCode(err) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break
        }
    }

    _filterKey(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    _getUnits() {
        this.setState({ isData: true });
        StorageHealthUnits.getHealthUnits()
            .then(data => this.setState({ data }))
            .catch(err => {
                this.setState({ data: [] })
                this._errStatusCode(err)
            })
            .finally(() => this.setState({ isData: true }));
    }

    _deleteUnit() {
        StorageHealthUnits.deleteHealth(this.state.unitCurrent.id)
            .then(() => this._alert.success('Unidade deletada!', () => this._getUnits()))
            .catch(err => this._errStatusCode(err))
            .finally(() =>   this.setState({ showModal: false }));
    }

    openDeleteModal(unitCurrent) {
        this.setState({ showModal: true, unitCurrent });
    }

    openRegisterModal() {
        this.setState({ unitCurrent: null, isEdit: false }, () => $('#modalUnits').modal('show'));
    }

    openEditModal(unitCurrent) {
        this.setState({ unitCurrent, isEdit: true }, () => $('#modalUnits').modal('show'));
    }

    componentDidMount() {
        if(this._storage.user.super_admin) {
            return this._getUnits();
        }
        Platform.withoutPermissionVizualization(this.props.history, '/patient');
    }

    render() {
        const { isData, isEdit, unitCurrent } = this.state;
        return (
            <div id="healthunit">
                <HeaderApp titleComponent="Unidades" />
                <ModalWarn
                    subtitle='Deletar'
                    twoButtonTitle='Cancelar'
                    oneButtonTitle='Confirmar'
                    oneButtonIcon='trash-alt'
                    title='Deletar unidade de Saúde'
                    visible={this.state.showModal}
                    oneButtonOnclick={() => this._deleteUnit()}
                    onClickAway={() => this.setState({ showModal: false })}
                    desc='Ao confirmar esta ação, a unidade de saúde será exclusa do sistema sem chances de recuperação de dados, deseja mesmo exclui-la?'
                />
                <Register
                    isEdit={isEdit}
                    unitCurrent={unitCurrent}
                    refresh={this._getUnits.bind(this)}
                />
                <ContainerHeader>
                    <Box
                        titleHeader='Gerenciamento de unidade de saúde/sintomatica.'
                        styleBox={{ borderTopLeftRadius: 7, borderTopRightRadius: 7 }}
                    >
                        <Row>
                            <GroupSearchTable
                                placeholderSearch="Pesquisar"
                                onFilter={this._filterKey.bind(this)}
                                onClickNewButton={this.openRegisterModal.bind(this)}
                            />
                        </Row>
                        <div className='container__table' style={{ marginTop: 15 }}>
                            <List
                                isData={isData}
                                logged={this._storage.health_current}
                                onEdit={this.openEditModal.bind(this)}
                                onDelete={this.openDeleteModal.bind(this)}
                                data={FilterList(this.state.data, this.state.filter, 'name') || []}
                            />
                        </div>
                    </Box>
                </ContainerHeader>
            </div>
        );
    }
}
export default healthUnit;
