import React from 'react';
import { Link } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import * as Icons from '@fortawesome/free-solid-svg-icons';
import { Line } from '../../../helpers';

const iconList = Object
    .keys(Icons)
    .filter(key => key !== "fas" && key !== "prefix")
    .map(icon => Icons[icon])

library.add(...iconList);

/**
 * @author Hector Rodrigues Da Silva
 * @SideBarMobile
 */
export const SideBarMobile = ({ userInfoSideBar }) =>
    <div className="sidebar__mobile">
        <ul>
            <Link to="/patients">
                <li title="Pacientes"><span>Pacientes&nbsp;<FontAwesomeIcon icon="procedures" /></span></li>
            </Link>
            <Line/>
            <Link to="/phonecalls">
                <li title="Unidades"><span>Ligações&nbsp;<FontAwesomeIcon icon="phone-alt" /></span></li>
            </Link>
            <Line/>
            {userInfoSideBar.user.super_admin &&
                <>
                    <Link to="/healthunit">
                        <li title="Unidades"><span>Unidades&nbsp;<FontAwesomeIcon icon="hospital-user" /></span></li>
                    </Link>
                    <Line />
                </>
            }
            {userInfoSideBar.user.type === "admin" &&
                <>
                    <Link to="/users">
                        <li title="Usuários"><span>Usuários&nbsp;<FontAwesomeIcon icon="user-friends" /></span></li>
                    </Link>
                    <Line />
                </>
            }
            {userInfoSideBar.user.type !== "municipal" &&
                <>
                    <Link to="/record/activity">
                        <li title="Registro de atividade"><span>Atividade&nbsp;<FontAwesomeIcon icon="clipboard-list"/></span></li>
                    </Link>
                    <Line />
                </>
            }
            <>
                <Link to="/help">
                    <li title="Ajuda"><span>Ajuda&nbsp;<FontAwesomeIcon icon="question-circle" /></span></li>
                    <Line />
                </Link>
            </>
        </ul>
    </div>;

export default SideBarMobile;
