import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestPatientUpdate extends Request {

    constructor(param, idPatient) {
        const url = `${Routes.PATIENTS}/${idPatient}`;
        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded'
        };
        super(url, "PUT", headers, param);
    }

}
