import './index.css';
/** @name Dependencies */
import React from 'react';
import $ from 'jquery';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

/** @name External */
import { Loader } from '../../../../helpers';

/**
 * @author Gustavo
 * @helper boxEmailRecovery helper
 */
export const BoxEmailRecovery = ({ nextRecovery, backRecovery, onChangeEmail, load, alertError }) =>
    <div className='container__boxEmail'>
        <div id={'one__boxEmail'} className='subdivision__card'>
            <button className={'button__back'} onClick={backRecovery}>
                <FontAwesomeIcon icon="arrow-left" style={{ cursor: 'pointer' }} color={'rgba(0, 184, 138, 1)'} />
            </button>
            <div className='container__text__informative' style={{ marginTop: '-70px' }}>
                <h1 className='text__title__boxEmail'>
                    Insira seu E-mail
                </h1>
                <h2 className='text__subtitle__boxEmail'>
                    Forneça seu E-mail ao lado para que possamos enviar o código de recuperação de senha.
                </h2>
            </div>
        </div>
        <div className='subdivision__card container__boxEmaill'>
            <div id={'mobile__boxEmail'} >
                <button className={'button__back'} onClick={backRecovery}>
                    <FontAwesomeIcon icon="arrow-left" />
                </button>
            </div>
            <div className='icon__custom__email'>
                <div className='iconCustom__boxEmail' style={{ paddingBottom: '0px' }}>
                    <FontAwesomeIcon className='iconCustomContent__boxEmail' icon="envelope" color={'white'} size={'2x'} />
                </div>
            </div>
            <div className={`alert alert-${alertError.alert} alert-dismissible show mt-3 alert__${alertError.className == "email" && alertError.className}`} role="alert">
                <strong>{alertError.title}</strong>&nbsp;{alertError.message}
                <button onClick={() => $('.alert').fadeOut('slow')} type="button" className="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className='container__card__boxEmail'>
                <div className='container__input__boxEmail'>
                    <FontAwesomeIcon className='icons__input__boxEmail' icon="user" />
                    <input
                        placeholder='E-mail'
                        className='input__reverse__Email'
                        onChange={event => onChangeEmail(event.target.value)}
                        onKeyPress={event => event.key === 'Enter' && nextRecovery()}
                    />
                </div>
            </div>
            <button className='button__boxEmail' onClick={nextRecovery}>
                {
                    load
                        ? <Loader size={10} />
                        : <h1 className='label__button__boxEmail'>ENVIAR</h1>
                }
            </button>
        </div>
    </div>;
