import { Request } from '../request';
import Routes from '../../../constants/routes';

export class RequestUserTransfer extends Request {

    constructor(id, healthUnit) {
        const url = `${Routes.USER}/${id}/transfer`;
        const headers = { 'Content-Type': 'application/x-www-form-urlencoded' };
        const params = {
            health_unit: healthUnit
        };
        super(url, "PATCH", headers, params);
    }

}
