import React from 'react';
import Chart from "react-apexcharts";
/**
 * @author Hector Rodrigues Da Silva
 * @helper Box
 */
export const ChartApex = ({ width, height, type, data, options, id }) =>
    <Chart
        id={id}
        type={type}
        series={data}
        width={width}
        height={height}
        options={options}
    />;
