import './css/index.css';
/** @name Dependencies */
import React from 'react';
import $, { data } from 'jquery';
import { withRouter } from 'react-router-dom';
/** @name Internal */
import { List } from './list';
import { SeeCalls } from './seeCalls';
import { SeeFamily } from './seeFamily';
import { DefineExam } from './defineExam';
import { PatientStorage } from './storage';
/** @name External */
import { Alert } from '../../factory/alert';
import { ControlPatient } from './controlPatient';
import { Platform } from '../../services/platform';
import {AsyncStorage} from "../../factory/storage";
import { FilterList } from '../../filters/filterTable';
import { HeaderApp, Row, ModalWarn, Box, BoxInfoTable, GroupSearchTable, ContainerHeader } from '../../helpers';

class Patients extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            filter: '',
            list: [],
            isList: false,
            seeFamilyPatient: [],
            seeCallsPatient: [],
            counter: {
                group_risk: 0,
                in_isolation: 0,
                total: 0,
                waiting_exam_result: 0
            },
            isModalDelete: {
                visible: false,
                idPatient: null
            },
            isModalDeath: {
                visible: false,
                idPatient: null
            },
            isModalDefineExam: {
                idPatient: null,
                namePatient: null
            },
            isEditPatient: false,
            isLoadSeeFamily: false,
            isLoadSeeCalls: false,
            isCurrentPatient: null
        };
        this._swal = new Alert();
        this._storage = AsyncStorage.getObject('user');
    }

    /**
     *
     * @param event
     * @private
     */
    _filterKey(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    /**
     *
     * @param err
     * @param callback
     */
    tratativeError(err, callback) {
        switch (err) {
            case 401:
                Platform.logout(this.props.history);
                break;
            case 500:
                Platform.internalError();
                break;
            case 400:
                Platform.clientError();
                break;
        }
        return callback;
    }

    /**
     *
     * @param idPatient
     * @method delete
     */
    _deletePatient(idPatient) {
        PatientStorage.deletePatient(idPatient)
            .then(() => {
                this.setState({ isModalDelete: { visible: false, idPatient: null } });
                this._swal.success('Paciente deletado!');
                this._refreshData();
            }).catch(err => err === 404 ? this.setState({ list: [] }) : this.tratativeError(err));
    }

    /**
     *
     * @param idPatient
     * @param exam
     * @private
     */
    _setExamPatient(idPatient, exam) {
        const examResult = { exam_result: exam };
        PatientStorage.setExamResultPatient(idPatient, examResult)
            .then(() => {
                this.setState({ isModalDefineExam: { idPatient: null, namePatient: null } });
                this._swal.success('Exame do paciente definido!', () => {
                    this._refreshData();
                    $('#modal_define_exam').modal('hide');
                });
            }).catch(err => this.tratativeError(err));
    }

    /**
     *
     * @param idPatient
     * @method patch
     */
    _setDeathPatient(idPatient) {
        PatientStorage.setDeathPatient(idPatient)
            .then(() => {
                this.setState({ isModalDeath: { visible: false, idPatient: null } });
                this._swal.success('Definição de óbito feita!', () => {
                    this._refreshData();
                })
            }).catch(err => this.tratativeError(err));
    }

    /**
     *
     * @param idPatient
     * @method get
     */
    _getCallsPatient(idPatient) {
        this.setState({ isLoadSeeCalls: true });
        PatientStorage.getCallsPatient(idPatient)
            .then(data => {
                this.setState({ seeCallsPatient: data });
            }).catch(err => {
                this.tratativeError(err);
            }).finally(() => this.setState({ isLoadSeeCalls: false }));
    }

    /**
     *
     * @param idPatient
     * @method get
     */
    _getFamiliyPatient(idPatient) {
        this.setState({ isLoadSeeFamily: true });
        PatientStorage.getFamilyPatient(idPatient)
            .then(data => {
                this.setState({ seeFamilyPatient: data });
            }).catch(err => {
                this.tratativeError(err);
            }).finally(() => this.setState({ isLoadSeeFamily: false }))
    }

    /**
     *
     * @method get
     */
    _getDataCounterPatient() {
        PatientStorage.getDataCounterPatients()
            .then(data => {
                this.setState({ counter: data });
            })
            .catch(err => err === 404 ? this.setState({ list: [] }) : this.tratativeError(err))
    }

    /**
     *
     * @method get
     */
    _getDataPatients() {
        this.setState({ isList: true });
        PatientStorage.getPatients()
            .then(data => this.setState({ list: data }))
            .catch(err => err === 404 ? this.setState({ list: [] }) : this.tratativeError(err))
            .finally(() => this.setState({ isList: false }));
    }

    /**
     *
     */
    resetIsEditPatient() {
        this.setState({ isEditPatient: false });
    }

    _refreshData() {
        this._getDataPatients();
        this._getDataCounterPatient();
    }

    componentDidMount() {
        this._refreshData();
        $('#see_family')
            .on('hidden.bs.modal', () => this.setState({ seeFamilyPatient: [] }));
        $('#see_calls')
            .on('hidden.bs.modal', () => this.setState({ seeCallsPatient: [] }));
    }

    render() {
        return (
            <>
                <HeaderApp titleComponent={'Pacientes'} icon="procedures" />
                <ContainerHeader>
                    <ControlPatient
                        refresh={() => this._refreshData()}
                        isEditPatient={this.state.isEditPatient}
                        currentIdPatient={this.state.isCurrentPatient}
                        resetIsEditPatient={this.resetIsEditPatient.bind(this)}
                    />
                    <SeeFamily
                        familyPatient={this.state.seeFamilyPatient}
                        isLoadSeeFamily={this.state.isLoadSeeFamily}
                    />
                    <SeeCalls
                        callsPatient={this.state.seeCallsPatient}
                        isLoadSeeCalls={this.state.isLoadSeeCalls}
                    />
                    <ModalWarn
                        subtitle={'Atenção'}
                        title={'Deletar paciente'}
                        oneButtonTitle={'Deletar'}
                        twoButtonTitle={'Cancelar'}
                        oneButtonIcon={'trash-alt'}
                        typeAnimation={'fadeInRight'}
                        visible={this.state.isModalDelete.visible}
                        oneButtonOnclick={() => this._deletePatient(this.state.isModalDelete.idPatient)}
                        onClickAway={() => this.setState({ isModalDelete: { visible: false, idPatient: null } })}
                        desc='Ao confirmar esta ação, o paciente e todos seus dados serão excluso do sistema sem chances de recuperação, deseja realmente continuar?'
                    />
                    <ModalWarn
                        subtitle={'Atenção'}
                        title={'Definir óbito do paciente'}
                        oneButtonTitle={'Confirmar'}
                        twoButtonTitle={'Cancelar'}
                        oneButtonIcon={'check'}
                        typeAnimation={'fadeInRight'}
                        visible={this.state.isModalDeath.visible}
                        oneButtonOnclick={() => this._setDeathPatient(this.state.isModalDeath.idPatient)}
                        onClickAway={() => this.setState({ isModalDeath: { visible: false, idPatient: null } })}
                        desc='Ao confirmar esta ação, você estará definindo o paciente a óbito, deseja realmente continuar?'

                    />
                    <DefineExam
                        idPatient={this.state.isModalDefineExam.idPatient}
                        namePatient={this.state.isModalDefineExam.namePatient}
                        onSubmitExamPatient={(idPatient, examResult) => this._setExamPatient(idPatient, examResult)}
                    />
                    <Box
                        titleHeader='Gerenciamento de pacientes em monitoramento'
                        styleBox={{ borderTopLeftRadius: 7, borderTopRightRadius: 7 }}
                    >
                        <div className="informations__data__table">
                            <BoxInfoTable
                                icon="procedures"
                                title="total"
                                count={this.state.counter.total}
                            />
                            <BoxInfoTable
                                icon="user-clock"
                                title="aguardando resultado"
                                count={this.state.counter.waiting_exam_result}
                            />
                            <BoxInfoTable
                                icon="house-user"
                                title="em isolamento"
                                count={this.state.counter.in_isolation}
                            />
                            <BoxInfoTable
                                icon="lungs-virus"
                                title="em grupo de risco"
                                count={this.state.counter.group_risk}
                            />
                        </div>
                        <Row>
                            <GroupSearchTable
                                placeholderSearch="Pesquisar"
                                onFilter={this._filterKey.bind(this)}
                                onClickNewButton={() => $('#control_patient').modal('show')}
                            />
                        </Row>
                        <div className="container__table">
                            <List
                                isData={this.state.isList}
                                logged={this._storage.user}
                                isGetFamilyPatient={(id) => this._getFamiliyPatient(id)}
                                isGetCallsPatient={(id) => this._getCallsPatient(id)}
                                data={FilterList(this.state.list, this.state.filter, 'name') || []}
                                isModalSetDeath={(setDeath) => this.setState({ isModalDeath: setDeath })}
                                isModalSetDelete={(objectUSer) => this.setState({ isModalDelete: objectUSer })}
                                isModalDefineExam={(resultExam) => this.setState({ isModalDefineExam: resultExam })}
                                isGetPatientEdit={(id) => this.setState({ isCurrentPatient: id, isEditPatient: true }, () => $('#control_patient').modal('show'))}
                            />
                        </div>
                    </Box>
                </ContainerHeader>
            </>
        );
    }
}

export default withRouter(Patients);
