import React from 'react';
import { Row } from '../../../helpers';
import Select from 'react-select';

export const Diagnostic = ({ onChangeFormValue, onChangeFormValueOption, infoPatient, isClassErrorsValues, isEdit }) =>
    <fieldset>
        <form>
            <Row>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Em isolamento:</label>
                    <Select
                        menuPortalTarget={document.body}
                        styles={{
                            menuPortal: styles => ({ ...styles, zIndex: 5000 })
                        }}
                        className={`${isClassErrorsValues.in_isolation}`}
                        value={isEdit ? {
                            label: infoPatient.in_isolation == 'Sim' && 'Sim'
                                || infoPatient.in_isolation == 'Não' && 'Não'
                                || 'Nennuma opção selecionada',
                            value: infoPatient.in_isolation == 'Sim' && 'Sim'
                                || infoPatient.in_isolation == 'Não' && 'Não'
                                || null
                        }
                         : 
                        {
                            label: !isEdit && !infoPatient.in_isolation ? 'Nenhuma opção selecionada' : infoPatient.in_isolation,
                            value: !isEdit && !infoPatient.in_isolation ? null : infoPatient.in_isolation
                        }}
                        options={[
                            { id: 'in_isolation', value: 'Sim', label: 'Sim' },
                            { id: 'in_isolation', value: 'Não', label: 'Não' },
                        ]}
                        placeholder={'Ver opções'}
                        onChange={onChangeFormValueOption}
                    />
                    <div className="invalid-feedback">
                        Seleciona alguma opção.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Primeira Avaliação Médica:</label>
                    <input type="text" id="first_evaluation" className="form-control" value={infoPatient.first_evaluation || ''} placeholder="Avaliação Médica" onChange={onChangeFormValue} />
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Data de inicio dos sintomas:</label>
                    <input type="date" id="early_symptoms" className="form-control" value={infoPatient.early_symptoms || ''} placeholder="Data de inicio dos sintomas" onChange={onChangeFormValue} />
                </div>
            </Row>
            <Row>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Data da coleta do exame:</label>
                    <input type="date" id="exam_collection" readOnly={isEdit} className={`form-control ${isClassErrorsValues.exam_collection}`} value={infoPatient.exam_collection || ''} placeholder="Data da coleta do exame" onChange={onChangeFormValue} />
                    <div className="invalid-feedback">
                        Preencha este campo.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Tipo de exame:</label>
                    <Select
                        menuPortalTarget={document.body}
                        styles={{
                            menuPortal: styles => ({ ...styles, zIndex: 5000 })
                        }}
                        isDisabled={isEdit}
                        className={`${isClassErrorsValues.exam_type}`}
                        value={{ label: infoPatient.exam_type || 'Nenhuma opção foi selecionada', value: infoPatient.exam_type || null }}
                        options={[
                            { id: 'exam_type', value: 'PCR-RT', label: 'PCR-RT' },
                            { id: 'exam_type', value: 'Sorologia', label: 'Sorologia' },
                            { id: 'exam_type', value: 'Teste Rápido', label: 'Teste Rápido' }
                        ]}
                        onChange={onChangeFormValueOption}
                        placeholder={'Ver opções'}
                    />
                    <div className="invalid-feedback">
                        Seleciona alguma opção.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xs-4 mb-3">
                    <label>Resultado do exame:</label>
                    <Select
                        menuPortalTarget={document.body}
                        styles={{
                            menuPortal: styles => ({ ...styles, zIndex: 5000 })
                        }}
                        isDisabled={isEdit}
                        className={`${isClassErrorsValues.exam_result}`}
                        value={{ label: infoPatient.exam_result || 'Nenhuma opção foi selecionada', value: infoPatient.exam_result || null }}
                        options={[
                            { id: 'exam_result', value: 'Positivo', label: 'Positivo' },
                            { id: 'exam_result', value: 'Negativo', label: 'Negativo' },
                            { id: 'exam_result', value: 'Aguardando Resultado', label: 'Aguardando Resultado' },
                        ]}
                        onChange={onChangeFormValueOption}
                        placeholder={'Ver opções'}
                    />
                    <div className="invalid-feedback">
                        Seleciona alguma opção.
                    </div>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-12 col-xs-12 mb-3">
                    <label>Data do resultado:</label>
                    <input type="date" id="exam_result_date" readOnly={isEdit} className="form-control" value={infoPatient.exam_result_date || ''} placeholder="Data de resultado do exame" onChange={onChangeFormValue} />
                </div>
            </Row>
        </form>
    </fieldset>;