import './index.css';

/** @name Dependencies */
import React from 'react';
import $ from 'jquery';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

/** @name External */
import { Loader } from '../../../../helpers';
/**
 * @author Gustavo
 * @helper boxPasswordRecovery helper
 */
export const BoxPasswordRecovery = ({ nextRecovery, visiblePassword, backRecovery, onChangeOne, onChangeTwo, showPass, load, alertError }) =>
    <div className='container__boxPassword'>
        <div id={'one'} className='subdivision__card'>
            <button className={'button__arrow__back'} onClick={backRecovery}>
                <FontAwesomeIcon icon="arrow-left" style={{ cursor: 'pointer' }} color={'white'} size={'2x'} />
            </button>
            <div className='container__text__informative'>
                <h1 className='text__title__boxPassword'>
                    Nova senha
                </h1>
                <h2 className='text__subtitle__boxPassword'>
                    Agora basta inserir sua nova senha. E para sua seguranças aplicamos algumas regras, tais como:
                </h2>
                <ul style={{ paddingTop: '5px' }}>
                    <li className='item__reqs'> <FontAwesomeIcon icon="arrow-alt-circle-right" /> Pelo menos 1 Número </li>
                    <li className='item__reqs'> <FontAwesomeIcon icon="arrow-alt-circle-right" /> Pelo menos 1 letra </li>
                    <li className='item__reqs'> <FontAwesomeIcon icon="arrow-alt-circle-right" /> Pelo menos 6 caracteres</li>
                </ul>
            </div>
        </div>
        <div id={'two'} className='subdivision__card container__boxLoginn'>
            <div className='icon__boxPassword' />
            <div className={`alert alert-${alertError.alert} alert-dismissible show mt-3 alert__${alertError.className == "password" && alertError.className}`} role="alert">
                <strong>{alertError.title}</strong>&nbsp;{alertError.message}
                <button onClick={() => $('.alert').fadeOut('slow')} type="button" className="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className='container__card__boxPassword'>
                <div className='container__input__boxPassword'>
                    <FontAwesomeIcon className='icons__input' icon="lock" color={'rgba(0, 184, 138, 1'} size={'2x'} />
                    <input
                        className='input'
                        placeholder='Senha'
                        type={visiblePassword ? 'password' : 'text'}
                        onChange={event => onChangeOne(event.target.value)}
                    />
                    <FontAwesomeIcon
                        size={'2x'}
                        onClick={showPass}
                        className='icons__input'
                        style={{ cursor: 'pointer' }}
                        color={'rgba(0, 184, 138, .7'}
                        icon={visiblePassword ? "eye" : "eye-slash"}
                    />
                </div>
            </div>
            <div className='container__card__boxPassword'>
                <div className='container__input__boxPassword'>
                    <FontAwesomeIcon className='icons__input' icon="lock" color={'rgba(0, 184, 138, 1'} size={'2x'} />
                    <input
                        className='input'
                        placeholder='Confirmar senha'
                        type={visiblePassword ? 'password' : 'text'}
                        onChange={event => onChangeTwo(event.target.value)}
                        onKeyPress={event => event.key === 'Enter' && nextRecovery()}
                    />
                </div>
            </div>
            <button className='button__boxPassword' onClick={nextRecovery} >
                {
                    load
                        ? <Loader size={10} color={'white'} />
                        : <h1 className='label__button__boxPassword'>ENTRAR</h1>
                }
            </button>
        </div>
    </div>
