import './css/index.css';
/** @name Dependencies */
import $ from "jquery";
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
/** @name External */
import { ModalTrans } from './modalTrans';
/**
 * @author Hector Rodrigues Da Silva
 * @class Dashboard
 */
class ChangeUnit extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            visibleModalUnits: false,
            isLoadModal: false
        }
    }

    showModalUnits() {
        $('#ModalTrans').modal('show');
        this.setState({ visibleModalUnits: true });
    }

    render() {
        return (
            <>
                <ModalTrans

                />
                <button
                    onClick={() => this.showModalUnits()}
                    className="button__change__unit" title="Trocar de unidade"
                >
                    <FontAwesomeIcon title="Trocar de unidade" icon="sitemap" />
                </button>
            </>
        );
    }
}


export default ChangeUnit;
