import './css/seeCalls.css';
import React, { Fragment } from 'react';
import $ from 'jquery';
import ReactTooltip from "react-tooltip";
import { MomentCustom } from '../../factory/moment';
import { Loader } from '../../helpers';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';

export const SeeCalls = ({ callsPatient, isLoadSeeCalls }) =>
    <div className="modal fade" id="see_calls" aria-hidden="true">
        <div className="modal-dialog modal-lg">
            <div className="modal-content">
                <div className="modal-header" style={{ backgroundColor: '#00b88a', color: 'white' }}>
                    <h5 className="modal-title">Ligações do paciente</h5>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    <div className="container__calls">
                        {isLoadSeeCalls
                            ?
                            <center>
                                <Loader size={15} />
                            </center>
                            :
                            callsPatient.length <= 0
                                ?
                                <center>
                                    Nenhuma ligação encontrada.
                                </center>
                                :
                                callsPatient.map((call, i) =>
                                    <Fragment key={i}>
                                        {call.completed && call.description ?
                                            <div>
                                                <>
                                                    <h5>Ligação {i + 1} - <strong>{MomentCustom.formatCustom(new Date(call.date), "DD/MM/YYYY")}</strong></h5>
                                                    <p>{call.description}</p>
                                                </>
                                            </div>
                                            :
                                            <div>
                                                <h5 style={{ borderBottom: '1px solid rgb(224, 224, 224)', paddingBottom: 10, paddingTop: 10 }}>Data para registro de ligação:&nbsp;
                                                    <strong>{
                                                        MomentCustom.formatCustom(new Date(call.date), "DD/MM/YYYY") == MomentCustom.formatCustom(new Date(), "DD/MM/YYYY")
                                                            ? <span>{MomentCustom.formatCustom(new Date(call.date), "DD/MM/YYYY")}
                                                                <b style={{ color: "rgb(223 134 0)", fontWeight: 'normal' }}>&nbsp;Registrar hoje&nbsp;
                                                                        <Link to="/phonecalls">
                                                                        <button
                                                                            data-for="button-new-register-calls"
                                                                            data-tip='Registrar ligação de hoje'
                                                                            onClick={() => $('#see_calls').modal('hide')}
                                                                            className="button__calls">
                                                                            <FontAwesomeIcon icon="phone-square" />
                                                                        </button>
                                                                        <ReactTooltip id='button-new-register-calls' place="top" type="dark" effect="solid" />
                                                                    </Link>
                                                                </b>
                                                            </span>

                                                            : MomentCustom.formatCustom(new Date(), "DD") > MomentCustom.formatCustom(new Date(call.date), "DD") ||
                                                                MomentCustom.formatCustom(new Date(), "MM") > MomentCustom.formatCustom(new Date(call.date), "MM") ||
                                                                MomentCustom.formatCustom(new Date(), "YYYY") > MomentCustom.formatCustom(new Date(call.date), "YYYY")
                                                                ? <span>{MomentCustom.formatCustom(new Date(call.date), "DD/MM/YYYY")}
                                                                    <b style={{ color: "#e30000", fontWeight: 'normal' }}>&nbsp;Não registrada&nbsp;
                                                                            <Link to="/phonecalls">
                                                                            <button
                                                                                data-for="button-not-register-calls"
                                                                                data-tip='Registrar ligação atrasada'
                                                                                onClick={() => $('#see_calls').modal('hide')}
                                                                                className="button__calls">
                                                                                <FontAwesomeIcon icon="phone-square" />
                                                                            </button>
                                                                            <ReactTooltip id='button-not-register-calls' place="top" type="dark" effect="solid" />
                                                                        </Link>
                                                                    </b>
                                                                </span>

                                                                : (MomentCustom.formatCustom(new Date(call.date), "MM") > MomentCustom.formatCustom(new Date(), "MM") ||
                                                                    MomentCustom.formatCustom(new Date(call.date), "DD") > MomentCustom.formatCustom(new Date(), "DD") ||
                                                                    MomentCustom.formatCustom(new Date(call.date), "YYYY") > MomentCustom.formatCustom(new Date(), "YYYY"))
                                                                && <span>{MomentCustom.formatCustom(new Date(call.date), "DD/MM/YYYY")}
                                                                    <b style={{ color: "rgb(0 159 86)", fontWeight: 'normal' }}>&nbsp;Registrar em breve&nbsp;
                                                                                <span style={{ fontSize: 15 }}><FontAwesomeIcon icon="calendar-day" /></span>
                                                                    </b>
                                                                </span>
                                                    }</strong>
                                                </h5>
                                            </div>}
                                    </Fragment>
                                )}
                    </div>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cancelar</button>
                </div>
            </div>
        </div>
    </div>;

export default SeeCalls;
